.ellipseIcon,
.goYounJung {
  position: relative;
  z-index: 2;
}
.ellipseIcon {
  height: 70px;
  width: 69px;
  border-radius: 50%;
  object-fit: cover;
}
.goYounJung {
  margin: 0;
  font-size: inherit;
  line-height: 25px;
  font-weight: 500;
  font-family: inherit;
}
.goYounJungWrapper,
.userInfo {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.goYounJungWrapper {
  flex-direction: column;
  padding: var(--padding-xl) 0 0;
}
.userInfo {
  flex-direction: row;
  gap: var(--gap-10xl);
  text-align: center;
  font-size: var(--font-size-13xl);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 750px) {
  .goYounJung {
    font-size: var(--font-size-7xl);
    line-height: 20px;
  }
  .userInfo {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 450px) {
  .goYounJung {
    font-size: var(--font-size-lgi);
    line-height: 15px;
  }
}
