.medanTop {
  width: 100%;
  height: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: var(--br-3xs);
  background-color: var(--color-gray-500);
  mix-blend-mode: soft-light;
}
.medanBottomIcon {
  align-self: stretch;
  flex: 1;
  position: relative;
  border-radius: var(--br-3xs);
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
  z-index: 1;
}
.medan {
  position: relative;
  font-weight: 500;
  display: inline-block;
  min-width: 39px;
  z-index: 2;
}
.medanCity {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-lg);
}
.mobileLegendTournament {
  align-self: stretch;
  position: relative;
  z-index: 2;
}
.medanLinkIcon {
  height: 10.3px;
  width: 7px;
  position: absolute;
  margin: 0 !important;
  top: -8.1px;
  left: 0;
  z-index: 2;
}
.medanLinkLabelChild {
  height: 30px;
  width: 94px;
  position: relative;
  border-radius: var(--br-3xs);
  background-color: var(--color-gold-100);
  display: none;
}
.readMore {
  position: relative;
  font-size: var(--font-size-3xs);
  font-weight: 600;
  font-family: var(--font-montserrat);
  color: var(--color-slategray-200);
  text-align: justify;
  display: inline-block;
  min-width: 56px;
  z-index: 4;
}
.medanLinkLabel {
  cursor: pointer;
  border: 0;
  padding: var(--padding-4xs) var(--padding-lgi);
  background-color: var(--color-gold-100);
  border-radius: var(--br-3xs);
  flex-direction: row;
  white-space: nowrap;
  z-index: 3;
}
.medanLinkLabel:hover {
  background-color: var(--color-goldenrod);
}
.medanLink,
.medanLinkLabel,
.medanTitle,
.medanTournament {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.medanLink {
  flex-direction: row;
  position: relative;
}
.medanTitle,
.medanTournament {
  flex-direction: column;
}
.medanTournament {
  align-self: stretch;
  gap: var(--gap-3xs);
  font-size: var(--font-size-sm);
}
.medanTitle {
  flex: 1;
  gap: var(--gap-8xs);
}
.medanContent,
.medanTopParent {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 var(--padding-3xs) 0 var(--padding-xl);
}
.medanTopParent {
  flex: 1;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  padding: 0 0 var(--padding-7xl);
  position: relative;
  gap: var(--gap-sm);
  text-align: justify;
  font-size: var(--font-size-2xs);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
