.frameChild,
.frameItem {
  position: absolute;
  top: 0;
  border-radius: var(--br-3xs);
}
.frameChild {
  left: 1px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  background-color: var(--color-gray-500);
  width: 100%;
  height: 100%;
  mix-blend-mode: soft-light;
}
.frameItem {
  left: 0;
  width: 264px;
  height: 189px;
  object-fit: cover;
  z-index: 1;
}
.mobileLegendTournament,
.vectorIcon {
  position: absolute;
  left: 20px;
  z-index: 2;
}
.mobileLegendTournament {
  top: 221px;
  display: inline-block;
  width: 234px;
  height: 36px;
}
.vectorIcon {
  top: 204px;
  width: 7px;
  height: 10px;
}
.tournamentReadMoreChild {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-3xs);
  background-color: var(--color-gold-100);
  width: 100%;
  height: 100%;
  display: none;
}
.readMore {
  position: absolute;
  top: 9px;
  left: 19px;
  font-size: var(--font-size-3xs);
  font-weight: 600;
  font-family: var(--font-montserrat);
  color: var(--color-slategray-200);
  text-align: justify;
  display: inline-block;
  min-width: 56px;
  z-index: 3;
}
.tournamentReadMore {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: var(--color-gold-100);
  position: absolute;
  top: 267px;
  left: 20px;
  border-radius: var(--br-3xs);
  width: 94px;
  height: 30px;
  white-space: nowrap;
  z-index: 2;
}
.tournamentReadMore:hover {
  background-color: var(--color-goldenrod);
}
.medan {
  position: absolute;
  top: 203px;
  left: 38px;
  font-size: var(--font-size-2xs);
  font-weight: 500;
  display: inline-block;
  min-width: 39px;
  z-index: 2;
}
.rectangleParent {
  position: absolute;
  top: 150px;
  left: 73px;
  width: 264px;
  height: 323px;
  z-index: 1;
  text-align: justify;
  font-size: var(--font-size-sm);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
