.userCommentChild {
  width: 658px;
  height: 872px;
  position: relative;
  border-radius: var(--br-xl);
  background-color: var(--color-gray-200);
  display: none;
  mix-blend-mode: normal;
  max-width: 100%;
}
.userAvatarIcon {
  width: 39px;
  height: 43px;
  position: relative;
  border-radius: 50%;
  object-fit: cover;
  z-index: 1;
}
.userAvatarContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-9xs) 0 0;
}
.goYounJung {
  margin-bottom: -17px;
  height: 30px;
  flex: 1;
  position: relative;
  line-height: 25px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  z-index: 1;
}
.userName {
  align-self: stretch;
  height: 13px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0;
  box-sizing: border-box;
}
.minutesAgo {
  width: 78px;
  position: relative;
  font-size: var(--font-size-3xs);
  line-height: 25px;
  color: var(--color-silver);
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 78px;
  flex-shrink: 0;
  z-index: 2;
}
.commentContent,
.commentElements,
.userNameTime {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.userNameTime {
  flex: 1;
  flex-direction: column;
}
.commentContent,
.commentElements {
  flex-direction: row;
}
.commentElements {
  flex: 1;
  gap: var(--gap-3xs);
}
.commentContent {
  width: 169px;
  padding: 0 var(--padding-6xs);
  box-sizing: border-box;
}
.slotMidBang {
  width: 152px;
  position: relative;
  line-height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
}
.userActionsIcon {
  align-self: stretch;
  flex: 1;
  position: relative;
  border-radius: var(--br-3xs);
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
  z-index: 1;
}
.commentActions {
  align-self: stretch;
  height: 394px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 var(--padding-base);
  box-sizing: border-box;
  gap: var(--gap-5xs);
  font-size: var(--font-size-xs);
}
.bookmarkIcon {
  height: 34px;
  width: 42px;
  position: relative;
  object-fit: contain;
  z-index: 1;
}
.heartReactIcon,
.laughReactIcon,
.likeReactIcon {
  height: 17px;
  width: 17px;
  position: relative;
  min-height: 17px;
  z-index: 1;
}
.heartReactIcon,
.laughReactIcon {
  cursor: pointer;
}
.reactionIcons {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.div,
.div1,
.div2 {
  position: relative;
  line-height: 25px;
  display: inline-block;
  min-width: 17px;
  z-index: 1;
}
.div1,
.div2 {
  min-width: 12px;
}
.div2 {
  min-width: 17px;
}
.reactionOptions,
.reactions {
  display: flex;
  align-items: flex-start;
}
.reactions {
  align-self: stretch;
  flex-direction: row;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.reactionOptions {
  width: 126px;
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-4xs);
}
.inputActions,
.inputOptions,
.userComment {
  display: flex;
  align-items: flex-start;
  max-width: 100%;
}
.inputOptions {
  flex: 1;
  flex-direction: row;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.inputActions,
.userComment {
  justify-content: flex-start;
  box-sizing: border-box;
}
.inputActions {
  width: 571px;
  flex-direction: row;
  padding: 0 var(--padding-6xs);
  font-size: var(--font-size-3xs);
}
.userComment {
  flex: 1;
  border-radius: var(--br-xl);
  background-color: var(--color-gray-200);
  flex-direction: column;
  padding: var(--padding-11xl) var(--padding-16xl) var(--padding-309xl);
  gap: var(--gap-2xs);
  min-width: 428px;
  text-align: center;
  font-size: var(--font-size-base);
  color: var(--color-white);
  font-family: var(--font-roboto);
}
@media screen and (max-width: 1200px) {
  .userComment {
    flex: 1;
  }
}
@media screen and (max-width: 1050px) {
  .userComment {
    padding-top: var(--padding-xl);
    padding-bottom: var(--padding-194xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .userComment {
    min-width: 100%;
  }
}
@media screen and (max-width: 450px) {
  .inputOptions {
    flex-wrap: wrap;
    justify-content: center;
  }
  .userComment {
    padding-bottom: var(--padding-119xl);
    box-sizing: border-box;
  }
}
