.contentAreaChild {
  height: 78px;
  width: 1452px;
  position: relative;
  background-color: var(--color-gold-200);
  display: none;
  max-width: 100%;
}
.chat1,
.vectorIcon {
  position: relative;
  z-index: 1;
}
.vectorIcon {
  height: 37px;
  width: 37px;
  cursor: pointer;
}
.chat1 {
  text-decoration: none;
  line-height: 25px;
  font-weight: 700;
  color: inherit;
  display: inline-block;
  min-width: 101px;
}
.subContent {
  flex-direction: column;
  align-items: flex-start;
  padding: var(--padding-8xs) 0 0;
}
.chat,
.contentArea,
.mainContainer,
.subContent {
  display: flex;
  justify-content: flex-start;
}
.contentArea {
  align-self: stretch;
  background-color: var(--color-gold-200);
  flex-direction: row;
  align-items: flex-start;
  padding: var(--padding-xl) var(--padding-34xl) var(--padding-2xl);
  box-sizing: border-box;
  gap: 546px;
  top: 0;
  z-index: 99;
  position: sticky;
  max-width: 100%;
  text-align: center;
  font-size: var(--font-size-29xl);
  color: var(--color-black);
  font-family: var(--font-roboto);
}
.chat,
.mainContainer {
  flex-direction: column;
}
.mainContainer {
  align-self: stretch;
  align-items: flex-end;
  gap: var(--gap-44xl);
  flex-shrink: 0;
  max-width: 100%;
}
.chat {
  width: 100%;
  height: 1854px;
  position: relative;
  background-color: var(--color-darkslateblue);
  overflow: hidden;
  align-items: flex-start;
  padding: 0 0 643px;
  box-sizing: border-box;
  gap: var(--gap-111xl);
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1350px) {
  .chat {
    gap: var(--gap-46xl);
  }
}
@media screen and (max-width: 800px) {
  .contentArea {
    gap: 273px;
    padding-left: var(--padding-7xl);
    padding-right: var(--padding-7xl);
    box-sizing: border-box;
  }
  .mainContainer {
    gap: var(--gap-12xl);
  }
  .chat {
    gap: var(--gap-13xl);
  }
}
@media screen and (max-width: 450px) {
  .contentArea {
    gap: 136px;
  }
  .chat,
  .mainContainer {
    gap: var(--gap-base);
  }
  .chat {
    height: auto;
  }
}
