.succesfullySaved,
.succesfullySavedWrapper {
  display: flex;
  justify-content: center;
  flex-shrink: 0;
}
.succesfullySaved {
  align-self: stretch;
  width: 256px;
  position: relative;
  line-height: 16px;
  align-items: center;
}
.succesfullySavedWrapper {
  margin-top: -770px;
  width: 656px;
  height: 16px;
  flex-direction: row;
  align-items: flex-start;
  padding: 0 var(--padding-xl);
  box-sizing: border-box;
  max-width: 100%;
}
.saveIcon {
  width: 43px;
  height: 43px;
  position: absolute;
  margin: 0 !important;
  top: 39px;
  left: 0;
  cursor: pointer;
  z-index: 1;
}
.accountName {
  width: 272px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-8xl) 0 0;
  box-sizing: border-box;
  min-width: 272px;
}
.saved {
  text-decoration: none;
  flex: 1;
  position: relative;
  font-size: var(--font-size-13xl);
  line-height: 16px;
  font-weight: 600;
  font-family: var(--font-montserrat);
  color: var(--color-white);
  text-align: center;
}
.frame43,
.frame43Wrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.frame43 {
  cursor: pointer;
  border: 0;
  padding: var(--padding-lgi) var(--padding-31xl);
  background-color: var(--color-slategray-100);
  width: 217px;
  border-radius: var(--br-3xs);
  overflow: hidden;
  flex-shrink: 0;
  justify-content: flex-start;
  box-sizing: border-box;
  z-index: 1;
}
.frame43:hover {
  background-color: var(--color-lightslategray);
}
.frame43Wrapper {
  align-self: stretch;
  justify-content: flex-end;
}
.username {
  width: 149px;
  position: relative;
  font-size: var(--font-size-5xl);
  line-height: 16px;
  font-weight: 600;
  font-family: var(--font-montserrat);
  color: var(--color-gold-100);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
}
.usernameActions {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-7xs);
}
.username2Child {
  height: 55px;
  width: 501px;
  position: relative;
  border-radius: var(--br-3xs);
  display: none;
  max-width: 100%;
}
.boesok {
  position: relative;
  font-size: var(--font-size-5xl);
  line-height: 16px;
  font-family: var(--font-roboto);
  color: var(--color-black);
  text-align: center;
  display: inline-block;
  min-width: 80px;
  z-index: 1;
}
.nickname,
.username2 {
  display: flex;
  z-index: 1;
}
.username2 {
  width: 501px;
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
  border: 0 solid var(--color-black);
  box-sizing: border-box;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lgi) var(--padding-xs) var(--padding-xl);
  max-width: 100%;
}
.nickname {
  width: 148px;
  position: relative;
  font-size: var(--font-size-5xl);
  line-height: 16px;
  font-weight: 600;
  font-family: var(--font-montserrat);
  color: var(--color-gold-100);
  text-align: center;
  align-items: center;
  justify-content: center;
}
.username2Item {
  height: 55px;
  width: 501px;
  position: relative;
  border-radius: var(--br-3xs);
  display: none;
  max-width: 100%;
}
.boesok14 {
  position: relative;
  font-size: var(--font-size-5xl);
  line-height: 16px;
  font-family: var(--font-roboto);
  color: var(--color-black);
  text-align: center;
  display: inline-block;
  min-width: 118px;
  z-index: 1;
}
.username21,
.usernameInput {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.username21 {
  width: 501px;
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
  border: 0 solid var(--color-black);
  box-sizing: border-box;
  flex-direction: row;
  padding: var(--padding-lgi) var(--padding-xs) var(--padding-xl);
  z-index: 1;
}
.usernameInput {
  align-self: stretch;
  flex-direction: column;
  gap: var(--gap-lg);
}
.rectangle50 {
  height: 55px;
  width: 174px;
  position: relative;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25) inset;
  border-radius: var(--br-3xs);
  background-color: var(--color-gold-100);
  display: none;
}
.save {
  flex: 1;
  position: relative;
  font-size: var(--font-size-13xl);
  line-height: 16px;
  font-weight: 600;
  font-family: var(--font-montserrat);
  color: var(--color-black);
  text-align: center;
  z-index: 2;
}
.rectangle50Parent,
.saveButton {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.rectangle50Parent {
  cursor: pointer;
  border: 0;
  padding: var(--padding-lgi) var(--padding-22xl) var(--padding-xl)
    var(--padding-23xl);
  background-color: var(--color-gold-100);
  flex: 1;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25) inset;
  border-radius: var(--br-3xs);
  justify-content: flex-start;
  z-index: 1;
}
.rectangle50Parent:hover {
  background-color: var(--color-goldenrod);
}
.saveButton {
  width: 232px;
  justify-content: flex-end;
  padding: 0 var(--padding-10xl);
  box-sizing: border-box;
}
.inputFields {
  margin: 0;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  gap: var(--gap-44xl);
  min-width: 341px;
  max-width: 100%;
}
.accountInfo,
.saveStatus {
  display: flex;
  align-items: flex-start;
  flex-shrink: 0;
  max-width: 100%;
}
.accountInfo {
  align-self: stretch;
  flex-direction: row;
  justify-content: center;
  gap: var(--gap-43xl);
}
.saveStatus {
  height: 511px;
  width: 1091px;
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-118xl) 0 0 var(--padding-213xl);
  box-sizing: border-box;
  position: relative;
  gap: var(--gap-735xl);
  text-align: center;
  font-size: var(--font-size-5xl);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 1125px) {
  .accountName {
    flex: 1;
  }
  .accountInfo {
    flex-wrap: wrap;
    gap: var(--gap-12xl);
  }
  .saveStatus {
    height: auto;
    gap: var(--gap-358xl);
    padding-left: var(--padding-97xl);
    padding-top: var(--padding-70xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 800px) {
  .save,
  .saved {
    font-size: var(--font-size-7xl);
    line-height: 13px;
  }
  .inputFields {
    gap: var(--gap-12xl);
    min-width: 100%;
  }
  .saveStatus {
    gap: var(--gap-169xl);
    padding-left: var(--padding-39xl);
    padding-top: var(--padding-39xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .saved,
  .succesfullySaved {
    font-size: var(--font-size-lgi);
    line-height: 13px;
  }
  .saved {
    line-height: 10px;
  }
  .boesok,
  .boesok14,
  .nickname,
  .save,
  .username {
    font-size: var(--font-size-lgi);
    line-height: 13px;
  }
  .save {
    line-height: 10px;
  }
  .inputFields {
    gap: var(--gap-base);
  }
  .accountInfo {
    gap: var(--gap-mini);
  }
  .saveStatus {
    gap: var(--gap-75xl);
    padding-left: var(--padding-xl);
    box-sizing: border-box;
  }
}
