.updatesDivider {
  align-self: stretch;
  height: 4px;
  position: relative;
  border-top: 4px solid var(--color-gold-100);
  box-sizing: border-box;
  z-index: 1;
}
.gameUpdate {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 25px;
  font-weight: 700;
  font-family: inherit;
}
.updatesTitleContainer {
  flex-direction: row;
  padding: 0 var(--padding-lg);
  box-sizing: border-box;
}
.gameUpdates,
.updatesHeader,
.updatesTitleContainer {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.updatesHeader {
  width: 1335px;
  flex-direction: column;
  gap: var(--gap-13xl);
}
.gameUpdates {
  flex-direction: row;
  padding: 0 var(--padding-27xl) var(--padding-74xl);
  box-sizing: border-box;
  flex-shrink: 0;
  text-align: center;
  font-size: var(--font-size-29xl);
  color: var(--color-gold-100);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 800px) {
  .gameUpdate {
    font-size: var(--font-size-19xl);
    line-height: 20px;
  }
  .updatesHeader {
    gap: var(--gap-base);
  }
  .gameUpdates {
    padding-left: var(--padding-4xl);
    padding-right: var(--padding-4xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .gameUpdate {
    font-size: var(--font-size-10xl);
    line-height: 15px;
  }
}
