.nerd {
  color: var(--color-red);
}
.nestNerd {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 20px;
  font-weight: 400;
  font-family: inherit;
  white-space: nowrap;
}
.nestNerdWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-2xl) 0 0;
}
.all {
  text-decoration: none;
  position: relative;
  color: inherit;
  display: inline-block;
  min-width: 22px;
}
.frameChild {
  width: 18px;
  height: 13px;
  position: relative;
}
.polygonWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-6xs-5) 0 0;
}
.allParent,
.frameItem {
  background-color: var(--color-gainsboro-300);
}
.allParent {
  width: 100px;
  border-radius: var(--br-8xs) 0 0 var(--br-8xs);
  border-right: 1px solid var(--color-black);
  box-sizing: border-box;
  overflow: hidden;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-6xs) var(--padding-5xl);
  gap: var(--gap-3xs);
  z-index: 2;
}
.frameItem {
  align-self: stretch;
  width: 469.7px;
  position: relative;
  border-radius: var(--br-3xs);
  display: none;
  max-width: 100%;
}
.vectorIcon {
  position: absolute;
  top: 7px;
  left: 430.7px;
  width: 25px;
  height: 27px;
  z-index: 1;
}
.frameInner {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-3xs);
  background-color: var(--color-gainsboro-300);
  width: 100%;
  height: 100%;
  display: none;
}
.rectangleGroup,
.vectorIcon1 {
  position: absolute;
  top: 7px;
  left: 430.7px;
  width: 25px;
  height: 27px;
  z-index: 1;
}
.rectangleGroup {
  top: 0;
  left: 0;
  border-radius: var(--br-3xs);
  background-color: var(--color-gainsboro-300);
  width: 100%;
  height: 100%;
  z-index: 3;
}
.vectorParent {
  height: 40px;
  flex: 1;
  position: relative;
  flex-shrink: 0;
  max-width: 100%;
}
.rectangleParent {
  flex: 1;
  border-radius: var(--br-3xs);
  background-color: var(--color-gainsboro-300);
  flex-direction: row;
  justify-content: flex-start;
  margin-left: -71.7px;
}
.frameContainer,
.frameWrapper,
.rectangleParent {
  display: flex;
  align-items: flex-start;
  max-width: 100%;
}
.frameContainer {
  align-self: stretch;
  flex-direction: row;
  justify-content: flex-start;
}
.frameWrapper {
  flex: 1;
  flex-direction: column;
  justify-content: flex-end;
  padding: 0 0 var(--padding-6xs);
  box-sizing: border-box;
}
.chatBubbleIcon,
.ellipseIcon {
  height: 59px;
  width: 55px;
  position: relative;
  object-fit: contain;
  cursor: pointer;
}
.ellipseIcon {
  width: 66px;
  height: 67px;
  border-radius: 50%;
  object-fit: cover;
}
.ellipseWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 var(--padding-12xs);
}
.chatBubbleParent {
  width: 160px;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.forumInner,
.frameGroup,
.frameParent {
  display: flex;
  flex-direction: row;
  max-width: 100%;
}
.frameGroup {
  width: 715px;
  align-items: flex-end;
  justify-content: flex-start;
  gap: var(--gap-38xl);
  font-size: var(--font-size-xl);
  color: var(--color-black);
  font-family: var(--font-average-sans);
}
.forumInner,
.frameParent {
  align-items: flex-start;
}
.frameParent {
  width: 1333px;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.forumInner {
  align-self: stretch;
  justify-content: flex-end;
  padding: 0 var(--padding-11xl);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--font-size-29xl);
  color: var(--color-white);
  font-family: var(--font-secular-one);
}
@media screen and (max-width: 800px) {
  .frameGroup {
    gap: var(--gap-9xl);
  }
}
