.instagramIcon {
  width: 31px;
  height: 32px;
  position: relative;
  object-fit: cover;
}
.instagramWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-11xs-5) 0 0;
}
.facebookIcon {
  width: 29px;
  height: 31px;
  position: relative;
  object-fit: cover;
}
