  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-11xs) 0 0;
}
.twitterxIcon,
.whatsappIcon {
  position: relative;
  object-fit: cover;
}
