  height: 35px;
  width: 31px;
  min-height: 35px;
}
.twitterxIcon {
  width: 25px;
  height: 25px;
}
.socialIcons,
.twitterxWrapper {
  display: flex;
  align-items: flex-start;
}
.twitterxWrapper {
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-8xs) 0 0;
}
.socialIcons {
  width: 266px;
  flex-direction: row;
  justify-content: space-between;
  flex-shrink: 0;
