.span,
.span1 {
  color: var(--color-black);
}
.span1 {
  white-space: pre-wrap;
}
.moreEvents1 {
  margin: 0;
  position: absolute;
  top: 0;
  left: 591px;
  font-size: inherit;
  font-weight: 700;
  font-family: inherit;
  z-index: 1;
}
.frameChild,
.image6 {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}
.image6 {
  left: 0;
  background: linear-gradient(180deg, #0c1d4a, rgba(237, 220, 220, 0));
  mix-blend-mode: lighten;
}
.frameChild {
  left: 1px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: var(--br-3xs);
  background-color: var(--color-gray-500);
  mix-blend-mode: soft-light;
}
.frameItem {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-3xs) var(--br-3xs) 0 0;
  width: 264px;
  height: 189px;
  object-fit: cover;
  z-index: 1;
}
.sagaE,
.tournamentPubgmOffline {
  margin: 0;
}
.tournamentPubgmOfflineContainer {
  position: absolute;
  top: 221px;
  left: 20px;
  display: inline-block;
  width: 234px;
  height: 36px;
  z-index: 2;
}
.vectorIcon {
  position: absolute;
  top: 204px;
  left: 20px;
  width: 7px;
  height: 10px;
  z-index: 2;
}
.frameInner {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-3xs);
  background-color: var(--color-gold-100);
  width: 100%;
  height: 100%;
  display: none;
}
.readMore {
  position: absolute;
  top: 9px;
  left: 19px;
  font-size: var(--font-size-3xs);
  font-weight: 600;
  font-family: var(--font-montserrat);
  color: var(--color-slategray-200);
  text-align: justify;
  display: inline-block;
  min-width: 56px;
  z-index: 3;
}
.rectangleGroup,
.surabaya {
  position: absolute;
  z-index: 2;
}
.rectangleGroup {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: var(--color-gold-100);
  top: 267px;
  left: 20px;
  border-radius: var(--br-3xs);
  width: 94px;
  height: 30px;
  white-space: nowrap;
}
.rectangleGroup:hover {
  background-color: var(--color-goldenrod);
}
.surabaya {
  top: 203px;
  left: 42px;
  font-size: var(--font-size-2xs);
  font-weight: 500;
  display: inline-block;
  min-width: 52px;
}
.rectangleParent {
  position: absolute;
  top: 150px;
  left: 1103px;
  width: 264px;
  height: 323px;
  z-index: 1;
}
.tournament {
  position: absolute;
  top: 31px;
  left: 0;
  width: 100%;
  height: 614px;
  font-size: var(--font-size-sm);
  color: var(--color-white);
}
.moreEvents {
  align-self: stretch;
  height: 645px;
  position: relative;
  text-align: justify;
  font-size: var(--font-size-13xl);
  color: var(--color-gold-100);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 800px) {
  .moreEvents1 {
    font-size: var(--font-size-7xl);
  }
}
@media screen and (max-width: 450px) {
  .moreEvents1 {
    font-size: var(--font-size-lgi);
  }
}
