.home {
  text-decoration: none;
  position: relative;
  line-height: 20px;
  color: inherit;
  display: inline-block;
  min-width: 75px;
  cursor: pointer;
}
.navigationItems {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) 0 0;
}
.comunity {
  text-decoration: none;
  position: relative;
  line-height: 20px;
  color: inherit;
  display: inline-block;
  min-width: 119px;
  cursor: pointer;
}
.navigationItems1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) 0 0;
}
.news {
  text-decoration: none;
  position: relative;
  line-height: 20px;
  color: inherit;
  display: inline-block;
  min-width: 67px;
  cursor: pointer;
}
.navigationItems2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) 0 0;
}
.forum {
  text-decoration: none;
  position: relative;
  line-height: 20px;
  color: inherit;
  display: inline-block;
  min-width: 82px;
  cursor: pointer;
}
.forumWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) 0 0;
}
.games1 {
  position: relative;
  line-height: 150%;
  display: inline-block;
  min-width: 85px;
}
.frameChild {
  width: 29.9px;
  height: 19px;
  position: relative;
  border-radius: var(--br-12xs);
}
.footer,
.frameParent,
.games,
.gamesInner,
.navigation {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.gamesInner {
  flex-direction: column;
  padding: var(--padding-4xs-5) 0 0;
}
.footer,
.frameParent,
.games,
.navigation {
  flex-direction: row;
}
.games {
  flex: 1;
  border-radius: var(--br-5xs);
  padding: var(--padding-3xs);
  gap: var(--gap-3xs);
}
.footer,
.frameParent,
.navigation {
  width: 347px;
  gap: var(--gap-11xl);
  max-width: 100%;
}
.footer,
.navigation {
  width: 820px;
  justify-content: space-between;
  padding: 0 var(--padding-xl) 0 0;
  box-sizing: border-box;
  gap: var(--gap-xl);
}
.footer {
  position: absolute;
  top: 32px;
  left: 75px;
  width: 1331px;
  flex-direction: column;
  justify-content: flex-start;
  padding: 0 0 var(--padding-519xl);
  gap: var(--gap-12xl);
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 800px) {
  .navigation {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 450px) {
  .comunity,
  .forum,
  .games1,
  .home,
  .news {
    font-size: var(--font-size-lgi);
    line-height: 16px;
  }
  .games1 {
    line-height: 29px;
  }
}
