.nerd {
  color: var(--color-red);
}
.nestNerd {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 20px;
  font-weight: 400;
  font-family: inherit;
}
.nestNerdWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-base) 0 0;
}
.frameChild {
  height: 40px;
  width: 469.7px;
  position: relative;
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
  display: none;
  max-width: 100%;
}
.vectorIcon {
  height: 27px;
  width: 25px;
  position: relative;
  z-index: 1;
}
.frameDiv,
.rectangleParent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  box-sizing: border-box;
  max-width: 100%;
}
.rectangleParent {
  align-self: stretch;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
  flex-direction: row;
  padding: var(--padding-6xs) var(--padding-sm) var(--padding-7xs);
}
.frameDiv {
  width: 469.7px;
  flex-direction: column;
  padding: 0 0 var(--padding-xs);
}
.chatBubbleIcon {
  width: 55px;
  height: 59px;
  position: relative;
  object-fit: cover;
  cursor: pointer;
}
.chatBubbleWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 var(--padding-12xs);
}
.ellipseIcon {
  height: 64px;
  width: 63px;
  position: relative;
  border-radius: 50%;
  object-fit: cover;
  cursor: pointer;
}
.frameContainer,
.frameGroup {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-start;
  gap: var(--gap-26xl-5);
  max-width: 100%;
}
.frameGroup {
  align-self: stretch;
  align-items: flex-start;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.home {
  text-decoration: none;
  position: relative;
  line-height: 20px;
  color: inherit;
  display: inline-block;
  min-width: 75px;
  cursor: pointer;
}
.menuOptions {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) 0 0;
}
.comunity {
  position: relative;
  line-height: 20px;
  display: inline-block;
  min-width: 119px;
  cursor: pointer;
}
.menuOptions1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) 0 0;
}
.news {
  text-decoration: none;
  position: relative;
  line-height: 20px;
  color: inherit;
  display: inline-block;
  min-width: 67px;
}
.menuOptions2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) 0 0;
}
.forum {
  text-decoration: none;
  position: relative;
  line-height: 20px;
  color: inherit;
  display: inline-block;
  min-width: 82px;
  cursor: pointer;
}
.forumWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) 0 0;
}
.games1 {
  position: relative;
  line-height: 150%;
  display: inline-block;
  min-width: 85px;
}
.frameItem {
  width: 29.9px;
  height: 19px;
  position: relative;
  border-radius: var(--br-12xs);
}
.frameParent1,
.games,
.gamesInner {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.gamesInner {
  flex-direction: column;
  padding: var(--padding-4xs-5) 0 0;
}
.frameParent1,
.games {
  flex-direction: row;
}
.games {
  flex: 1;
  border-radius: var(--br-5xs);
  padding: var(--padding-3xs);
  box-sizing: border-box;
  gap: var(--gap-3xs);
  min-width: 115px;
}
.frameParent1 {
  width: 336px;
  gap: var(--gap-lgi);
  max-width: 100%;
}
.menuOptionsParent {
  width: 789px;
  flex-direction: row;
  justify-content: space-between;
  gap: var(--gap-xl);
  font-size: var(--font-size-5xl);
  font-family: var(--font-montserrat);
}
.frameParent,
.frameWrapper,
.menuOptionsParent {
  display: flex;
  align-items: flex-start;
  max-width: 100%;
}
.frameParent {
  flex: 1;
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-20xl);
}
.frameWrapper {
  width: 1417px;
  flex-direction: row;
  justify-content: flex-end;
  padding: 0 var(--padding-17xl);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--font-size-29xl);
  color: var(--color-white);
  font-family: var(--font-secular-one);
}
@media screen and (max-width: 1125px) {
  .frameGroup {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 825px) {
  .nestNerd {
    font-size: var(--font-size-19xl);
    line-height: 16px;
  }
  .frameContainer {
    flex-wrap: wrap;
    gap: var(--gap-4xl);
  }
  .menuOptionsParent {
    flex-wrap: wrap;
  }
  .frameParent {
    gap: var(--gap-lgi);
  }
}
@media screen and (max-width: 450px) {
  .nestNerd {
    font-size: var(--font-size-10xl);
    line-height: 12px;
  }
  .comunity,
  .forum,
  .games1,
  .home,
  .news {
    font-size: var(--font-size-lgi);
    line-height: 16px;
  }
  .games1 {
    line-height: 29px;
  }
  .frameParent1 {
    flex-wrap: wrap;
  }
}
