.nerd {
  color: var(--color-red);
}
.nestNerd {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 20px;
  font-weight: 400;
  font-family: inherit;
  cursor: pointer;
}
.nestNerdWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-2xl) 0 0;
}
.frameChild {
  height: 40px;
  width: 469.7px;
  position: relative;
  border-radius: var(--br-3xs);
  background-color: var(--color-gainsboro-300);
  display: none;
  max-width: 100%;
}
.vectorIcon {
  height: 27px;
  width: 25px;
  position: relative;
  z-index: 1;
}
.frameWrapper1,
.rectangleParent {
  display: flex;
  align-items: flex-start;
  box-sizing: border-box;
  max-width: 100%;
}
.rectangleParent {
  align-self: stretch;
  border-radius: var(--br-3xs);
  background-color: var(--color-gainsboro-300);
  flex-direction: row;
  justify-content: flex-end;
  padding: var(--padding-6xs) var(--padding-sm) var(--padding-7xs);
}
.frameWrapper1 {
  flex: 1;
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-smi) 0 0;
  min-width: 305px;
}
.chatBubbleIcon {
  width: 55px;
  height: 59px;
  position: relative;
  object-fit: cover;
  cursor: pointer;
}
.chatBubbleWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-11xs) 0 0;
}
.ellipseIcon {
  height: 67px;
  width: 66px;
  position: relative;
  border-radius: 50%;
  object-fit: cover;
}
.frameContainer,
.frameDiv {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  max-width: 100%;
}
.frameDiv {
  width: 669px;
  justify-content: flex-start;
  gap: var(--gap-20xl-2);
}
.frameContainer {
  align-self: stretch;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.comunity,
.home {
  text-decoration: none;
  position: relative;
  line-height: 20px;
  color: inherit;
  display: inline-block;
  min-width: 65px;
  cursor: pointer;
}
.comunity {
  min-width: 102px;
}
.homeComunityContent {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.homeComunityContentWrapper {
  width: 251px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) var(--padding-2xl) 0 0;
  box-sizing: border-box;
}
.news {
  text-decoration: none;
  position: relative;
  line-height: 20px;
  color: inherit;
  display: inline-block;
  min-width: 61px;
  cursor: pointer;
}
.newsForumContent {
  width: 85px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) var(--padding-xl) 0 0;
  box-sizing: border-box;
}
.forum {
  text-decoration: none;
  position: relative;
  line-height: 20px;
  color: inherit;
  display: inline-block;
  min-width: 70px;
  cursor: pointer;
}
.newsForumContent1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) 0 0;
}
.games1 {
  position: relative;
  line-height: 150%;
  display: inline-block;
  min-width: 76px;
}
.frameItem {
  width: 29.9px;
  height: 19px;
  position: relative;
  border-radius: var(--br-12xs);
}
.gamesInner {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-4xs-5) 0 0;
}
.frameParent1,
.games {
  flex-direction: row;
  justify-content: flex-start;
}
.games {
  width: 235px;
  border-radius: var(--br-5xs);
  display: flex;
  align-items: flex-start;
  padding: var(--padding-3xs);
  box-sizing: border-box;
  gap: var(--gap-3xs);
}
.frameParent1 {
  gap: var(--gap-23xl);
  font-size: var(--font-size-5xl);
  font-family: var(--font-roboto);
}
.frameGroup,
.frameParent1,
.frameWrapper {
  display: flex;
  align-items: flex-start;
  max-width: 100%;
}
.frameGroup {
  flex: 1;
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-3xl);
}
.frameWrapper {
  width: 1411px;
  flex-direction: row;
  justify-content: flex-end;
  padding: 0 var(--padding-23xl);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--font-size-29xl);
  color: var(--color-white);
  font-family: var(--font-secular-one);
}
.frameInner {
  height: 554px;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
  z-index: 1;
}
.pubgPlayerUnknown {
  margin: 0 !important;
  width: 774px;
  position: absolute;
  right: 314px;
  bottom: 74px;
  font-size: inherit;
  line-height: 25px;
  font-weight: 700;
  font-family: inherit;
  display: inline-block;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  z-index: 2;
}
.frameParent,
.vectorParent {
  align-self: stretch;
  display: flex;
  justify-content: flex-start;
  max-width: 100%;
}
.vectorParent {
  flex-direction: row;
  align-items: flex-start;
  position: relative;
}
.frameParent {
  flex-direction: column;
  align-items: flex-end;
  padding: 0 var(--padding-12xs) var(--padding-144xl) 0;
  box-sizing: border-box;
  gap: var(--gap-18xl);
  text-align: center;
  font-size: var(--font-size-21xl);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 1350px) {
  .frameWrapper {
    padding-left: var(--padding-2xl);
    padding-right: var(--padding-2xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1125px) {
  .frameContainer {
    flex-wrap: wrap;
  }
  .frameParent {
    padding-bottom: var(--padding-87xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 800px) {
  .nestNerd {
    font-size: var(--font-size-19xl);
    line-height: 16px;
  }
  .frameDiv {
    gap: var(--gap-xl);
  }
  .frameDiv,
  .frameParent1 {
    flex-wrap: wrap;
  }
  .pubgPlayerUnknown {
    font-size: var(--font-size-13xl);
    line-height: 20px;
  }
  .frameParent {
    gap: var(--gap-lg);
  }
}
@media screen and (max-width: 450px) {
  .nestNerd {
    font-size: var(--font-size-10xl);
    line-height: 12px;
  }
  .comunity,
  .forum,
  .games1,
  .home,
  .news {
    font-size: var(--font-size-lgi);
    line-height: 16px;
  }
  .games1 {
    line-height: 29px;
  }
  .frameParent1 {
    gap: var(--gap-2xl);
  }
  .pubgPlayerUnknown {
    font-size: var(--font-size-5xl);
    line-height: 15px;
  }
  .frameParent {
    padding-bottom: var(--padding-50xl);
    box-sizing: border-box;
  }
}
