.nerd {
  color: var(--color-red);
}
.nestNerd {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 20px;
  font-weight: 400;
  font-family: inherit;
  white-space: nowrap;
}
.nestNerdWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 var(--padding-7xs);
}
.frameChild {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-3xs);
  background-color: var(--color-gainsboro-300);
  width: 100%;
  height: 100%;
  display: none;
}
.vectorIcon {
  position: absolute;
  top: 7px;
  left: 430.7px;
  width: 25px;
  height: 27px;
  z-index: 1;
}
.frameItem,
.rectangleParent {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-3xs);
  background-color: var(--color-gainsboro-300);
  width: 100%;
  height: 100%;
}
.frameItem {
  display: none;
}
.rectangleGroup,
.vectorIcon1 {
  position: absolute;
  top: 7px;
  left: 430.7px;
  width: 25px;
  height: 27px;
  z-index: 1;
}
.rectangleGroup {
  top: 0;
  left: 0;
  border-radius: var(--br-3xs);
  background-color: var(--color-gainsboro-300);
  width: 100%;
  height: 100%;
  z-index: 3;
}
.frameDiv {
  position: absolute;
  top: 0;
  left: 28.3px;
  width: 469.7px;
  height: 40px;
}
.all {
  text-decoration: none;
  position: relative;
  color: inherit;
  display: inline-block;
  min-width: 22px;
}
.frameInner {
  width: 18px;
  height: 13px;
  position: relative;
}
.allParent,
.polygonWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-6xs-5) 0 0;
}
.allParent {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-8xs) 0 0 var(--br-8xs);
  background-color: var(--color-gold-100);
  border-right: 1px solid var(--color-black);
  box-sizing: border-box;
  width: 100px;
  overflow: hidden;
  flex-direction: row;
  padding: var(--padding-6xs) var(--padding-5xl);
  gap: var(--gap-3xs);
  z-index: 4;
}
.frameContainer {
  align-self: stretch;
  height: 40px;
  position: relative;
}
.frameWrapper {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 var(--padding-7xs);
  box-sizing: border-box;
  max-width: 100%;
}
.vectorIcon2 {
  width: 39px;
  height: 41px;
  position: relative;
}
.vectorWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 var(--padding-3xs-7);
}
.ellipseIcon {
  height: 67px;
  width: 66px;
  position: relative;
  border-radius: 50%;
  object-fit: cover;
}
.frameGroup,
.frameParent1 {
  align-items: flex-end;
  justify-content: flex-start;
}
.frameParent1 {
  display: flex;
  flex-direction: row;
  gap: var(--gap-11xl);
}
.frameGroup {
  width: 715px;
  gap: var(--gap-63xl);
  font-size: var(--font-size-xl);
  color: var(--color-black);
  font-family: var(--font-average-sans);
}
.frameGroup,
.frameParent,
.mainContentInner {
  display: flex;
  flex-direction: row;
  max-width: 100%;
}
.frameParent {
  flex: 1;
  align-items: flex-end;
  justify-content: space-between;
  gap: var(--gap-xl);
  text-align: left;
  font-size: var(--font-size-29xl);
  color: var(--color-white);
  font-family: var(--font-secular-one);
}
.mainContentInner {
  align-self: stretch;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 0 var(--padding-8xs);
  box-sizing: border-box;
}
@media screen and (max-width: 800px) {
  .frameGroup {
    gap: var(--gap-22xl);
  }
}
@media screen and (max-width: 450px) {
  .frameGroup {
    gap: var(--gap-xl);
  }
}
