.eventTitleValues,
.frameChild {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.frameChild {
  height: calc(100% - 203px);
  margin: 0 !important;
  right: 0;
  bottom: 203px;
  background-color: var(--color-darkslategray-600);
  z-index: 1;
}
.eventTitleValues {
  border-radius: var(--br-3xs);
  height: 100%;
  object-fit: cover;
  cursor: pointer;
  z-index: 2;
}
.fps {
  margin: 0;
  position: absolute;
  top: 258px;
  left: 133px;
  font-size: inherit;
  line-height: 20px;
  font-weight: 700;
  font-family: inherit;
  display: inline-block;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  min-width: 129px;
  z-index: 3;
}
.eventTitles,
.eventTitlesChild {
  height: 503px;
  position: relative;
  max-width: 100%;
}
.eventTitlesChild {
  width: 359px;
  border-radius: var(--br-3xs);
  object-fit: cover;
  display: none;
}
.moba {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 20px;
  font-weight: 700;
  font-family: inherit;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  z-index: 3;
}
.eventTitles1,
.eventTitlesItem {
  height: 503px;
  border-radius: var(--br-3xs);
  max-width: 100%;
}
.eventTitles1 {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-end;
  padding: var(--padding-213xl) var(--padding-39xl);
  box-sizing: border-box;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  z-index: 2;
}
.eventTitlesItem {
  width: 359px;
  position: relative;
  object-fit: cover;
  display: none;
}
.rpg {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 20px;
  font-weight: 700;
  font-family: inherit;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  z-index: 3;
}
.eventTitles2,
.rectangleParent {
  flex-direction: row;
  box-sizing: border-box;
  max-width: 100%;
}
.eventTitles2 {
  height: 503px;
  border-radius: var(--br-3xs);
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding: var(--padding-216xl) var(--padding-xl) var(--padding-216xl)
    var(--padding-20xl);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  z-index: 2;
}
.rectangleParent {
  align-self: stretch;
  display: grid;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-xs) var(--padding-115xl) var(--padding-159xl)
    var(--padding-122xl);
  position: relative;
  gap: var(--gap-25xl);
  min-height: 743px;
  grid-template-columns: repeat(3, minmax(269px, 1fr));
  text-align: left;
  font-size: var(--font-size-45xl);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 1125px) {
  .rectangleParent {
    justify-content: center;
    grid-template-columns: repeat(2, minmax(269px, 467px));
  }
}
@media screen and (max-width: 800px) {
  .fps,
  .moba {
    font-size: var(--font-size-32xl);
    line-height: 16px;
  }
  .eventTitles1 {
    padding-top: var(--padding-132xl);
    padding-bottom: var(--padding-132xl);
    box-sizing: border-box;
  }
  .rpg {
    font-size: var(--font-size-32xl);
    line-height: 16px;
  }
  .eventTitles2 {
    padding-top: var(--padding-134xl);
    padding-bottom: var(--padding-134xl);
    box-sizing: border-box;
  }
  .rectangleParent {
    gap: var(--gap-3xl);
    padding: var(--padding-xl) var(--padding-48xl) var(--padding-97xl)
      var(--padding-51xl);
    box-sizing: border-box;
    grid-template-columns: minmax(269px, 1fr);
  }
}
@media screen and (max-width: 450px) {
  .fps,
  .moba {
    font-size: var(--font-size-19xl);
    line-height: 12px;
  }
  .eventTitles1 {
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
  .rpg {
    font-size: var(--font-size-19xl);
    line-height: 12px;
  }
  .rectangleParent {
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
}
