.frameChild {
  height: 57px;
  width: 177px;
  position: relative;
  border-radius: var(--br-3xs);
  background-color: var(--color-gold-100);
  display: none;
}
.latest {
  position: relative;
  font-size: var(--font-size-5xl);
  line-height: 25px;
  font-family: var(--font-montserrat);
  color: var(--color-black);
  text-align: center;
  display: inline-block;
  min-width: 74px;
  z-index: 1;
}
.rectangleParent {
  cursor: pointer;
  border: 0;
  padding: var(--padding-base) var(--padding-32xl);
  background-color: var(--color-gold-100);
  border-radius: var(--br-3xs);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.rectangleParent:hover {
  background-color: var(--color-goldenrod);
}
.frameItem {
  height: 57px;
  width: 177px;
  position: relative;
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
  display: none;
}
.info {
  position: relative;
  font-size: var(--font-size-5xl);
  line-height: 25px;
  font-family: var(--font-montserrat);
  color: var(--color-black);
  text-align: center;
  display: inline-block;
  min-width: 47px;
  z-index: 1;
}
.frameInner,
.rectangleGroup {
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
}
.rectangleGroup {
  cursor: pointer;
  border: 0;
  padding: var(--padding-base) var(--padding-46xl);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.groupButton:hover,
.rectangleContainer:hover,
.rectangleGroup:hover {
  background-color: var(--color-gainsboro-100);
}
.frameInner {
  height: 57px;
  width: 177px;
  position: relative;
  display: none;
}
.update {
  position: relative;
  font-size: var(--font-size-5xl);
  line-height: 25px;
  font-family: var(--font-montserrat);
  color: var(--color-black);
  text-align: center;
  display: inline-block;
  min-width: 90px;
  z-index: 1;
}
.rectangleContainer,
.rectangleDiv {
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
}
.rectangleContainer {
  cursor: pointer;
  border: 0;
  padding: var(--padding-base) var(--padding-24xl);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.rectangleDiv {
  height: 57px;
  width: 177px;
  position: relative;
  display: none;
}
.event {
  position: relative;
  font-size: var(--font-size-5xl);
  line-height: 25px;
  font-family: var(--font-montserrat);
  color: var(--color-black);
  text-align: center;
  display: inline-block;
  min-width: 69px;
  z-index: 1;
}
.frameParent,
.groupButton {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.groupButton {
  cursor: pointer;
  border: 0;
  padding: var(--padding-base) var(--padding-35xl);
  background-color: var(--color-white);
  border-radius: var(--br-3xs);
  justify-content: flex-start;
}
.frameParent {
  justify-content: center;
  gap: var(--gap-11xl);
  max-width: 100%;
}
@media screen and (max-width: 1125px) {
  .frameParent {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 450px) {
  .event,
  .info,
  .latest,
  .update {
    font-size: var(--font-size-lgi);
    line-height: 20px;
  }
}
