.comunity,
.home {
  position: relative;
  line-height: 20px;
  display: inline-block;
  cursor: pointer;
}
.home {
  text-decoration: none;
  color: inherit;
  min-width: 65px;
}
.comunity {
  min-width: 102px;
}
.frameWrapper,
.homeParent {
  display: flex;
  align-items: flex-start;
}
.homeParent {
  align-self: stretch;
  flex-direction: row;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.frameWrapper {
  width: 251px;
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-lg) var(--padding-2xl) 0 0;
  box-sizing: border-box;
}
.news {
  text-decoration: none;
  position: relative;
  line-height: 20px;
  color: inherit;
  display: inline-block;
  min-width: 61px;
  cursor: pointer;
}
.newsForumTitle {
  width: 85px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) var(--padding-xl) 0 0;
  box-sizing: border-box;
}
.forum1 {
  text-decoration: none;
  position: relative;
  line-height: 20px;
  color: inherit;
  display: inline-block;
  min-width: 70px;
  cursor: pointer;
}
.newsForumTitle1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) 0 0;
}
.games1 {
  position: relative;
  line-height: 150%;
  display: inline-block;
  min-width: 76px;
  cursor: pointer;
}
.frameChild {
  width: 29.9px;
  height: 19px;
  position: relative;
  border-radius: var(--br-12xs);
}
.forumInner,
.frameParent,
.games,
.gamesInner {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.gamesInner {
  flex-direction: column;
  padding: var(--padding-4xs-5) 0 0;
}
.forumInner,
.frameParent,
.games {
  flex-direction: row;
}
.games {
  width: 235px;
  border-radius: var(--br-5xs);
  padding: var(--padding-3xs);
  box-sizing: border-box;
  gap: var(--gap-3xs);
}
.forumInner,
.frameParent {
  max-width: 100%;
}
.frameParent {
  gap: var(--gap-23xl);
}
.forumInner {
  padding: 0 var(--padding-58xl);
  box-sizing: border-box;
}
.frameItem {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-11xl);
  background-color: var(--color-white);
  width: 100%;
  height: 100%;
}
.whatAreYou {
  position: absolute;
  top: 14px;
  left: 164px;
  z-index: 1;
}
.rectangleParent {
  height: 58px;
  flex: 1;
  position: relative;
  min-width: 402px;
  max-width: 100%;
  cursor: pointer;
}
.vectorIcon {
  height: 56.8px;
  width: 60px;
  position: relative;
}
.frameContainer,
.frameDiv {
  display: flex;
  flex-direction: row;
  max-width: 100%;
}
.frameDiv {
  width: 714px;
  flex-wrap: wrap;
  align-items: flex-end;
  justify-content: flex-start;
  gap: var(--gap-16xl);
}
.frameContainer {
  width: 1084px;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl);
  box-sizing: border-box;
}
.infoTitleChild {
  width: 1165px;
  height: 872px;
  position: relative;
  border-radius: var(--br-3xs);
  background-color: var(--color-darkslategray-400);
  display: none;
  max-width: 100%;
}
.infoMabarValo {
  position: relative;
  line-height: 25px;
  z-index: 1;
}
.infoMabarValoCeesWrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-base);
}
.infoTitleItem {
  align-self: stretch;
  height: 569px;
  position: relative;
  border-radius: var(--br-3xs);
  max-width: 100%;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
  z-index: 1;
}
.infoTitle,
.infoTitleInner {
  border-radius: var(--br-3xs);
  background-color: var(--color-darkslategray-400);
  max-width: 100%;
}
.infoTitle {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-xs) var(--padding-45xl) var(--padding-10xl)
    var(--padding-51xl);
  box-sizing: border-box;
  gap: 26.3px;
}
.infoTitleInner {
  width: 1165px;
  height: 872px;
  position: relative;
  display: none;
}
.slotMidBang {
  position: relative;
  line-height: 25px;
  z-index: 1;
}
.slotMidBangCapekSoloWrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-18xl) var(--padding-5xs);
  box-sizing: border-box;
  max-width: 100%;
}
.frameInner {
  height: 569px;
  flex: 1;
  position: relative;
  border-radius: var(--br-3xs);
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
  z-index: 1;
}
.infoTitle1,
.infoTitleInner1 {
  align-self: stretch;
  align-items: flex-start;
  box-sizing: border-box;
}
.infoTitleInner1 {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  padding: 0 0 0 var(--padding-11xs);
  max-width: 100%;
}
.infoTitle1 {
  border-radius: var(--br-3xs);
  background-color: var(--color-darkslategray-400);
  padding: var(--padding-3xs) var(--padding-43xl) var(--padding-sm)
    var(--padding-51xl);
  gap: var(--gap-2xl);
}
.frameGroup,
.infoTitle1,
.infoTitleParent {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  max-width: 100%;
}
.infoTitleParent {
  align-self: stretch;
  align-items: flex-start;
  gap: var(--gap-25xl);
  text-align: center;
  color: var(--color-white);
  font-family: var(--font-roboto);
}
.frameGroup {
  width: 1165px;
  align-items: flex-end;
  gap: var(--gap-33xl);
}
.forum,
.forumChild {
  display: flex;
  align-items: flex-start;
  box-sizing: border-box;
  text-align: left;
  font-size: var(--font-size-5xl);
}
.forumChild {
  width: 1425px;
  flex-direction: row;
  justify-content: center;
  padding: 0 var(--padding-xl);
  max-width: 100%;
  color: var(--color-black);
  font-family: var(--font-average-sans);
}
.forum {
  width: 100%;
  position: relative;
  background-color: var(--color-darkslateblue);
  overflow: hidden;
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-11xl) 0 0;
  gap: 64.3px;
  line-height: normal;
  letter-spacing: normal;
  color: var(--color-white);
  font-family: var(--font-roboto);
}
@media screen and (max-width: 1350px) {
  .infoTitle,
  .infoTitle1 {
    padding-left: var(--padding-16xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
  }
  .infoTitle1 {
    padding-right: var(--padding-12xl);
  }
}
@media screen and (max-width: 1125px) {
  .forumInner {
    padding-left: var(--padding-19xl);
    padding-right: var(--padding-19xl);
    box-sizing: border-box;
  }
  .infoTitle {
    padding-top: var(--padding-xl);
    padding-bottom: var(--padding-xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 800px) {
  .frameParent {
    flex-wrap: wrap;
  }
  .rectangleParent {
    min-width: 100%;
  }
  .frameDiv {
    gap: var(--gap-mid);
  }
  .infoTitleParent {
    gap: var(--gap-3xl);
  }
  .frameGroup {
    gap: var(--gap-7xl);
  }
  .forum {
    gap: var(--gap-13xl);
  }
}
@media screen and (max-width: 450px) {
  .comunity,
  .forum1,
  .games1,
  .home,
  .news {
    font-size: var(--font-size-lgi);
    line-height: 16px;
  }
  .games1 {
    line-height: 29px;
  }
  .frameParent {
    gap: var(--gap-2xl);
  }
  .infoMabarValo,
  .slotMidBang,
  .whatAreYou {
    font-size: var(--font-size-lgi);
  }
  .infoMabarValo,
  .slotMidBang {
    line-height: 20px;
  }
  .forum {
    gap: var(--gap-base);
  }
}
