.nerd {
  color: var(--color-red);
}
.nestNerd {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 20px;
  font-weight: 400;
  font-family: inherit;
}
.nestNerdWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-xs) 0 0;
}
.frameChild {
  height: 40px;
  width: 469.7px;
  position: relative;
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
  display: none;
  max-width: 100%;
}
.vectorIcon {
  height: 27px;
  width: 25px;
  position: relative;
  z-index: 1;
}
.frameWrapper,
.rectangleParent {
  display: flex;
  align-items: flex-start;
  box-sizing: border-box;
  max-width: 100%;
}
.rectangleParent {
  align-self: stretch;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
  flex-direction: row;
  justify-content: flex-end;
  padding: var(--padding-6xs) var(--padding-sm) var(--padding-7xs);
}
.frameWrapper {
  flex: 1;
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-xs) 0 0;
  min-width: 305px;
}
.chatbubbleIcon,
.ellipseIcon {
  height: 59px;
  width: 55px;
  position: relative;
  object-fit: cover;
  cursor: pointer;
}
.ellipseIcon {
  height: 64px;
  width: 63px;
  border-radius: 50%;
}
.frameParent {
  width: 663px;
  justify-content: flex-start;
  gap: var(--gap-18xl-6);
}
.frameParent,
.leftColumn,
.leftColumnWrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  max-width: 100%;
}
.leftColumn {
  width: 1338px;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.leftColumnWrapper {
  align-self: stretch;
  justify-content: flex-end;
  padding: 0 var(--padding-22xl) var(--padding-31xl);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--font-size-29xl);
  color: var(--color-white);
  font-family: var(--font-secular-one);
}
@media screen and (max-width: 1125px) {
  .leftColumn {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 800px) {
  .nestNerd {
    font-size: var(--font-size-19xl);
    line-height: 16px;
  }
  .frameParent {
    flex-wrap: wrap;
    gap: var(--gap-lgi);
  }
}
@media screen and (max-width: 450px) {
  .nestNerd {
    font-size: var(--font-size-10xl);
    line-height: 12px;
  }
}
