}
.anotherFacebookSet1 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  gap: var(--gap-3xs);
  max-width: 100%;
}
.anotherFacebookSetInner {
  height: 427px;
  flex: 1;
