  object-fit: contain;
  z-index: 1;
}
.anotherFacebookSet2,
.anotherFacebookSetParent {
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.anotherFacebookSet2 {
  display: flex;
  position: relative;
  gap: var(--gap-3xs);
}
.anotherFacebookSetParent {
  align-self: stretch;
  display: grid;
  gap: var(--gap-47xl);
  grid-template-columns: repeat(3, minmax(291px, 1fr));
}
.frameContainer,
.frameWrapper {
  display: flex;
  justify-content: flex-start;
  max-width: 100%;
}
.frameContainer {
  flex: 1;
  flex-direction: column;
  align-items: center;
}
.frameWrapper {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  padding: 0 0 0 var(--padding-sm);
  box-sizing: border-box;
}
.syncEsportCommunity {
  display: inline-block;
  max-width: 100%;
}
.heroCommunity,
.syncEsportCommunity {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 25px;
  font-weight: 700;
  font-family: inherit;
}
.heroCommunityWrapper {
  width: 334px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 var(--padding-12xs);
  box-sizing: border-box;
  max-width: 100%;
}
.jawaraCommunity {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 25px;
  font-weight: 700;
  font-family: inherit;
  display: inline-block;
  max-width: 100%;
}
.frameParent,
.sYNC {
  width: 1279px;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
  max-width: 100%;
  gap: var(--gap-xl);
}
.frameParent {
  width: 1330px;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-xl) var(--padding-13xl) 0;
  box-sizing: border-box;
  gap: var(--gap-29xl);
  flex-shrink: 0;
  text-align: center;
  font-size: var(--font-size-13xl);
  color: var(--color-gold-100);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 1200px) {
  .anotherFacebookSetParent {
    justify-content: center;
    grid-template-columns: repeat(2, minmax(291px, 504px));
  }
  .sYNC {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 825px) {
  .anotherFacebookSetParent {
    gap: var(--gap-14xl);
    grid-template-columns: minmax(291px, 1fr);
  }
  .frameContainer {
    gap: var(--gap-5xl);
  }
  .heroCommunity,
  .jawaraCommunity,
  .syncEsportCommunity {
    font-size: var(--font-size-7xl);
    line-height: 20px;
  }
  .frameParent {
    gap: var(--gap-5xl);
  }
}
@media screen and (max-width: 450px) {
  .anotherFacebookSetParent {
    gap: var(--gap-base);
  }
  .heroCommunity,
  .jawaraCommunity,
  .syncEsportCommunity {
    font-size: var(--font-size-lgi);
    line-height: 15px;
  }
}
