.pubgBannerChild {
  height: 554px;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
  z-index: 1;
}
.pubgPlayerUnknown {
  margin: 0 !important;
  width: 774px;
  position: absolute;
  right: 314px;
  bottom: 74px;
  font-size: inherit;
  line-height: 25px;
  font-weight: 700;
  font-family: inherit;
  display: inline-block;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  z-index: 2;
}
.newsItemParent,
.pubgBanner {
  align-self: stretch;
  display: flex;
  justify-content: flex-start;
  max-width: 100%;
}
.pubgBanner {
  flex-direction: row;
  align-items: flex-start;
  position: relative;
}
.newsItemParent {
  flex-direction: column;
  align-items: flex-end;
  padding: 0 var(--padding-12xs) var(--padding-144xl) 0;
  box-sizing: border-box;
  gap: var(--gap-18xl);
  text-align: center;
  font-size: var(--font-size-21xl);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 1125px) {
  .newsItemParent {
    padding-bottom: var(--padding-87xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 800px) {
  .pubgPlayerUnknown {
    font-size: var(--font-size-13xl);
    line-height: 20px;
  }
  .newsItemParent {
    gap: var(--gap-lg);
  }
}
@media screen and (max-width: 450px) {
  .pubgPlayerUnknown {
    font-size: var(--font-size-5xl);
    line-height: 15px;
  }
  .newsItemParent {
    padding-bottom: var(--padding-50xl);
    box-sizing: border-box;
  }
}
