.nerd {
  color: var(--color-red);
}
.nestNerd {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 20px;
  font-weight: 400;
  font-family: inherit;
}
.contentElement {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 var(--padding-4xl);
}
.all {
  text-decoration: none;
  position: relative;
  color: inherit;
  display: inline-block;
  min-width: 22px;
}
.frameChild {
  width: 18px;
  height: 13px;
  position: relative;
}
.subItemInner {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-6xs-5) 0 0;
}
.frameItem,
.subItem {
  background-color: var(--color-gainsboro-300);
}
.subItem {
  width: 100px;
  border-radius: var(--br-8xs) 0 0 var(--br-8xs);
  border-right: 1px solid var(--color-black);
  box-sizing: border-box;
  overflow: hidden;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-6xs) var(--padding-5xl);
  gap: var(--gap-3xs);
  z-index: 2;
}
.frameItem {
  align-self: stretch;
  width: 469.7px;
  position: relative;
  border-radius: var(--br-3xs);
  display: none;
  max-width: 100%;
}
.vectorIcon {
  position: absolute;
  top: 7px;
  left: 431.7px;
  width: 25px;
  height: 27px;
  z-index: 1;
}
.frameInner {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-3xs);
  background-color: var(--color-gainsboro-300);
  width: 100%;
  height: 100%;
  display: none;
}
.rectangleGroup,
.vectorIcon1 {
  position: absolute;
  top: 7px;
  left: 430.7px;
  width: 25px;
  height: 27px;
  z-index: 1;
}
.rectangleGroup {
  top: 0;
  left: 0;
  border-radius: var(--br-3xs);
  background-color: var(--color-gainsboro-300);
  width: 100%;
  height: 100%;
  z-index: 3;
}
.vectorParent {
  margin-left: -1px;
  height: 40px;
  flex: 1;
  position: relative;
  max-width: 100%;
}
.rectangleParent {
  flex: 1;
  border-radius: var(--br-3xs);
  background-color: var(--color-gainsboro-300);
  flex-direction: row;
  flex-shrink: 0;
  margin-left: -71.7px;
}
.rectangleParent,
.subContent,
.subElement {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.subContent {
  align-self: stretch;
  flex-direction: row;
}
.subElement {
  flex: 1;
  flex-direction: column;
  padding: var(--padding-smi) 0 0;
  box-sizing: border-box;
  min-width: 324px;
}
.chatBubbleIcon {
  width: 55px;
  height: 59px;
  position: relative;
  object-fit: cover;
  cursor: pointer;
}
.chatBubbleParent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-12xs) 0 0;
}
.ellipseIcon {
  height: 67px;
  width: 66px;
  position: relative;
  border-radius: 50%;
  object-fit: cover;
  cursor: pointer;
}
.bubbleContainer {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-10xl);
}
.element,
.innerContent {
  display: flex;
  flex-direction: row;
  max-width: 100%;
}
.element {
  width: 683px;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-16xl);
  font-size: var(--font-size-xl);
  color: var(--color-black);
  font-family: var(--font-average-sans);
}
.innerContent {
  align-self: stretch;
  align-items: flex-end;
  justify-content: space-between;
  gap: var(--gap-xl);
  text-align: left;
  font-size: var(--font-size-29xl);
  color: var(--color-white);
  font-family: var(--font-secular-one);
}
@media screen and (max-width: 1050px) {
  .innerContent {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 750px) {
  .nestNerd {
    font-size: var(--font-size-19xl);
    line-height: 16px;
  }
  .element {
    flex-wrap: wrap;
    gap: var(--gap-mid);
  }
}
@media screen and (max-width: 450px) {
  .nestNerd {
    font-size: var(--font-size-10xl);
    line-height: 12px;
  }
  .all {
    font-size: var(--font-size-base);
  }
}
