.carouselItemsIcon {
  align-self: stretch;
  position: relative;
  min-height: 325px;
}
.carouselItemsIcon,
.carouselItemsIcon1,
.imageCarouselChild {
  width: 278px;
  border-radius: var(--br-3xs);
  max-height: 100%;
  object-fit: cover;
}
.carouselItemsIcon1 {
  align-self: stretch;
  position: relative;
  min-height: 325px;
  cursor: pointer;
}
.imageCarouselChild {
  height: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: -114px;
  bottom: 0;
}
.freeFire,
.imageCarousel {
  position: relative;
  z-index: 1;
}
.imageCarousel {
  width: 920px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-81xl);
  max-width: 100%;
}
.freeFire {
  margin: 0;
  font-size: inherit;
  line-height: 25px;
  font-weight: 800;
  font-family: inherit;
}
.otherGames {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-7xl) 0 0;
}
.pubg {
  display: inline-block;
  min-width: 122px;
}
.pubg,
.valorant {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 25px;
  font-weight: 800;
  font-family: inherit;
  z-index: 1;
}
.popularGames {
  width: 965px;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 0 0 var(--padding-xl);
  box-sizing: border-box;
  gap: var(--gap-xl);
}
.gameHighlights,
.gameTitles,
.highlightContent,
.popularGames {
  display: flex;
  align-items: flex-start;
  max-width: 100%;
}
.gameTitles {
  align-self: stretch;
  flex-direction: row;
  justify-content: flex-end;
}
.gameHighlights,
.highlightContent {
  justify-content: flex-start;
}
.highlightContent {
  width: 1006px;
  flex-direction: column;
  padding: 0 var(--padding-xl) 0 0;
  box-sizing: border-box;
  gap: var(--gap-26xl);
}
.gameHighlights {
  position: absolute;
  top: 2340px;
  left: 217px;
  width: 1189px;
  flex-direction: row;
  text-align: center;
  font-size: var(--font-size-21xl);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 800px) {
  .imageCarousel {
    flex-wrap: wrap;
  }
  .freeFire,
  .pubg,
  .valorant {
    font-size: var(--font-size-13xl);
    line-height: 20px;
  }
  .popularGames {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 450px) {
  .freeFire,
  .pubg,
  .valorant {
    font-size: var(--font-size-5xl);
    line-height: 15px;
  }
}
