.comunity,
.home {
  position: relative;
  line-height: 20px;
  display: inline-block;
  cursor: pointer;
}
.home {
  text-decoration: none;
  color: inherit;
  min-width: 75px;
}
.comunity {
  min-width: 119px;
}
.homeComunity {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.homeComunityWrapper {
  width: 279px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) var(--padding-3xl) 0 0;
  box-sizing: border-box;
}
.news {
  text-decoration: none;
  position: relative;
  line-height: 20px;
  color: inherit;
  display: inline-block;
  min-width: 67px;
  cursor: pointer;
}
.newsContent {
  width: 92px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) var(--padding-xl) 0 0;
  box-sizing: border-box;
}
.forum {
  text-decoration: none;
  position: relative;
  line-height: 20px;
  color: inherit;
  display: inline-block;
  min-width: 82px;
  cursor: pointer;
}
.newsContent1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) 0 0;
}
.games1 {
  position: relative;
  line-height: 150%;
  display: inline-block;
  min-width: 85px;
}
.frameChild {
  width: 29.9px;
  height: 19px;
  position: relative;
  border-radius: var(--br-12xs);
}
.gamesInner {
  flex-direction: column;
  padding: var(--padding-4xs-5) 0 0;
}
.frameGroup,
.games,
.gamesInner,
.newsForum {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.games {
  width: 235px;
  border-radius: var(--br-5xs);
  flex-direction: row;
  padding: var(--padding-3xs);
  box-sizing: border-box;
  gap: var(--gap-3xs);
}
.frameGroup,
.newsForum {
  max-width: 100%;
}
.newsForum {
  flex-direction: row;
  padding: 0 var(--padding-xl) 0 0;
  box-sizing: border-box;
  gap: var(--gap-22xl);
  font-size: var(--font-size-5xl);
  font-family: var(--font-montserrat);
}
.frameGroup {
  align-self: stretch;
  flex-direction: column;
  gap: var(--gap-29xl);
  text-align: left;
  font-size: var(--font-size-29xl);
  color: var(--color-white);
  font-family: var(--font-secular-one);
}
.frameItem {
  height: 57px;
  width: 177px;
  position: relative;
  border-radius: var(--br-5xs) 0 0 var(--br-5xs);
  background-color: var(--color-white);
  border: 1px solid var(--color-black);
  box-sizing: border-box;
  display: none;
}
.twitter {
  position: relative;
  font-size: var(--font-size-5xl);
  line-height: 25px;
  font-family: var(--font-montserrat);
  color: var(--color-black);
  text-align: center;
  display: inline-block;
  min-width: 84px;
  z-index: 1;
}
.rectangleParent {
  cursor: pointer;
  border: 1px solid var(--color-black);
  padding: var(--padding-sm) var(--padding-27xl);
  background-color: var(--color-white);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: var(--br-5xs) 0 0 var(--br-5xs);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.frameInner,
.rectangleParent:hover {
  background-color: var(--color-gainsboro-100);
  border: 1px solid var(--color-darkslategray-300);
  box-sizing: border-box;
}
.frameInner {
  height: 57px;
  width: 177px;
  position: relative;
  background-color: var(--color-gold-100);
  border: 1px solid var(--color-black);
  display: none;
}
.facebook {
  position: relative;
  font-size: var(--font-size-5xl);
  line-height: 25px;
  font-family: var(--font-montserrat);
  color: var(--color-black);
  text-align: center;
  display: inline-block;
  min-width: 118px;
  z-index: 1;
}
.rectangleGroup {
  cursor: pointer;
  border: 1px solid var(--color-black);
  padding: var(--padding-sm) var(--padding-10xl);
  background-color: var(--color-gold-100);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.groupButton:hover,
.rectangleDiv,
.rectangleGroup:hover {
  background-color: var(--color-goldenrod);
  border: 1px solid var(--color-darkslategray-300);
  box-sizing: border-box;
}
.rectangleDiv {
  height: 57px;
  width: 177px;
  position: relative;
  background-color: var(--color-white);
  border: 1px solid var(--color-black);
  display: none;
}
.whatsaap {
  position: relative;
  line-height: 25px;
  display: inline-block;
  min-width: 124px;
  z-index: 1;
}
.frameChild1,
.rectangleContainer {
  background-color: var(--color-white);
  border: 1px solid var(--color-black);
}
.rectangleContainer {
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-sm) var(--padding-7xl);
}
.frameChild1 {
  height: 57px;
  width: 177px;
  position: relative;
  border-radius: 0 var(--br-5xs) var(--br-5xs) 0;
  box-sizing: border-box;
  display: none;
}
.tiktok {
  position: relative;
  font-size: var(--font-size-5xl);
  line-height: 25px;
  font-family: var(--font-montserrat);
  color: var(--color-black);
  text-align: center;
  display: inline-block;
  min-width: 74px;
  z-index: 1;
}
.frameDiv,
.groupButton {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.groupButton {
  cursor: pointer;
  border: 1px solid var(--color-black);
  padding: var(--padding-sm) var(--padding-32xl);
  background-color: var(--color-white);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 0 var(--br-5xs) var(--br-5xs) 0;
  justify-content: flex-start;
}
.groupButton:hover {
  background-color: var(--color-gainsboro-100);
}
.frameDiv {
  justify-content: center;
  max-width: 100%;
  row-gap: 20px;
}
.frameContainer {
  flex-direction: row;
  padding: 0 var(--padding-6xs);
  box-sizing: border-box;
}
.frameContainer,
.frameParent,
.frameWrapper {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.frameParent {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-43xl);
}
.frameWrapper {
  align-self: stretch;
  flex-direction: row;
  padding: 0 0 0 var(--padding-3xs);
  box-sizing: border-box;
  text-align: center;
  font-size: var(--font-size-5xl);
  color: var(--color-black);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 1200px) {
  .newsForum {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 825px) {
  .frameGroup {
    gap: var(--gap-5xl);
  }
  .frameDiv {
    flex-wrap: wrap;
  }
  .frameParent {
    gap: var(--gap-12xl);
  }
}
@media screen and (max-width: 450px) {
  .comunity,
  .forum,
  .games1,
  .home,
  .news {
    font-size: var(--font-size-lgi);
    line-height: 16px;
  }
  .games1 {
    line-height: 29px;
  }
  .newsForum {
    gap: var(--gap-xl);
  }
  .facebook,
  .tiktok,
  .twitter,
  .whatsaap {
    font-size: var(--font-size-lgi);
    line-height: 20px;
  }
  .frameParent {
    gap: var(--gap-mini);
  }
}
