.statusContainerChild {
  height: 123px;
  width: 493px;
  position: relative;
  border-radius: var(--br-xl);
  background-color: var(--color-dimgray-200);
  display: none;
  max-width: 100%;
}
.anotherStatusIcon {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 4;
}
.connectionStatusOn {
  position: absolute;
  top: 7px;
  left: 74px;
  width: 19px;
  height: 31px;
  object-fit: contain;
  z-index: 5;
}
.connectionDetails {
  height: 101px;
  width: 98px;
  position: relative;
}
.lisaManoba {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 25px;
  font-weight: 600;
  font-family: inherit;
  z-index: 4;
}
.anotherUserMessage {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-8xs);
}
.halloAkuGyj {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-xl);
  line-height: 25px;
  font-weight: 500;
  color: var(--color-gainsboro-200);
  z-index: 4;
}
.anotherChatHistory,
.anotherMessageThread {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.anotherMessageThread {
  align-self: stretch;
  justify-content: flex-start;
  gap: var(--gap-9xs);
}
.anotherChatHistory {
  flex: 1;
  justify-content: flex-end;
  padding: 0 0 var(--padding-2xl);
  box-sizing: border-box;
  min-width: 206px;
}
.anotherUserProfile,
.statusContainer {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  box-sizing: border-box;
  max-width: 100%;
}
.statusContainer {
  flex: 1;
  border-radius: var(--br-xl);
  background-color: var(--color-dimgray-200);
  flex-wrap: wrap;
  align-items: flex-end;
  padding: var(--padding-3xs) var(--padding-base) var(--padding-xs)
    var(--padding-14xl);
  gap: var(--gap-10xl);
  z-index: 3;
}
.anotherUserProfile {
  align-self: stretch;
  align-items: flex-start;
  padding: 0 var(--padding-9xs) 0 var(--padding-12xs);
  text-align: center;
  font-size: var(--font-size-9xl);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 450px) {
  .halloAkuGyj,
  .lisaManoba {
    font-size: var(--font-size-3xl);
    line-height: 20px;
  }
  .halloAkuGyj {
    font-size: var(--font-size-base);
  }
}
