.community,
.communityInner,
.frameParent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.frameParent {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-66xl);
  max-width: 100%;
}
.community,
.communityInner {
  box-sizing: border-box;
}
.communityInner {
  width: 1354px;
  flex-direction: row;
  padding: 0 var(--padding-sm);
  max-width: 100%;
  flex-shrink: 0;
}
.community {
  width: 100%;
  height: 2193px;
  position: relative;
  background-color: var(--color-darkslateblue);
  overflow: hidden;
  flex-direction: column;
  padding: var(--padding-22xl) 0 676px var(--padding-39xl);
  gap: 99px;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1425px) {
  .community {
    padding-left: var(--padding-10xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1200px) {
  .community {
    height: auto;
  }
}
@media screen and (max-width: 825px) {
  .frameParent {
    gap: var(--gap-23xl);
  }
  .community {
    gap: var(--gap-30xl);
  }
}
@media screen and (max-width: 450px) {
  .frameParent {
    gap: var(--gap-2xl);
  }
  .community {
    gap: var(--gap-6xl);
  }
}
