.footerContent {
  flex: 1;
  display: grid;
  gap: var(--gap-59xl);
  grid-template-columns: repeat(4, minmax(201px, 1fr));
}
.footer {
  width: 1402px;
  display: flex;
  padding: 0 var(--padding-35xl);
  box-sizing: border-box;
  text-align: justify;
  font-size: var(--font-size-2xs);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 1350px) {
  .footer {
    padding-left: var(--padding-8xl);
    padding-right: var(--padding-8xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1125px) {
  .footerContent {
    justify-content: center;
    grid-template-columns: repeat(2, minmax(201px, 348px));
  }
}
@media screen and (max-width: 800px) {
  .footerContent {
    gap: var(--gap-20xl);
    grid-template-columns: minmax(201px, 1fr);
  }
}
@media screen and (max-width: 450px) {
  .footerContent {
    gap: var(--gap-lgi);
  }
}
