.featuresImageContainer {
  align-self: stretch;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
  z-index: 1;
}
.fiturBaruDi {
  flex: 1;
  position: relative;
  line-height: 25px;
  font-weight: 800;
  z-index: 1;
}
.featuresTitleContainer,
.newFeatures {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.featuresTitleContainer {
  align-self: stretch;
  flex-direction: row;
  padding: 0 var(--padding-6xs) 0 var(--padding-11xs);
}
.newFeatures {
  height: 349px;
  width: 329px;
  flex-shrink: 0;
  flex-direction: column;
  gap: var(--gap-5xs);
  max-width: 100%;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 450px) {
  .fiturBaruDi {
    font-size: var(--font-size-lgi);
    line-height: 20px;
  }
}
