.content {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-37xl);
  max-width: 100%;
}
.main {
  width: 1337px;
  flex-direction: row;
  padding: 0 var(--padding-lg);
  max-width: 100%;
}
.comment,
.main,
.userCommentParent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
}
.userCommentParent {
  width: 1336px;
  flex-direction: row;
  padding: 0 var(--padding-xl) var(--padding-59xl) 0;
  max-width: 100%;
  row-gap: 20px;
}
.comment {
  width: 100%;
  position: relative;
  background-color: var(--color-darkslateblue);
  overflow: hidden;
  flex-direction: column;
  padding: var(--padding-19xl) var(--padding-11xs) var(--padding-5xs)
    var(--padding-40xl);
  gap: var(--gap-139xl);
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1200px) {
  .userCommentParent {
    flex-wrap: wrap;
  }
  .comment {
    gap: var(--gap-60xl);
    padding-left: var(--padding-10xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1050px) {
  .userCommentParent {
    padding-bottom: var(--padding-32xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .content {
    gap: var(--gap-9xl);
  }
  .userCommentParent {
    padding-bottom: var(--padding-14xl);
    box-sizing: border-box;
  }
  .comment {
    gap: var(--gap-20xl);
  }
}
@media screen and (max-width: 450px) {
  .comment {
    gap: var(--gap-xl);
  }
}
