.firstIconShape,
.hourAgo {
  position: relative;
  z-index: 1;
}
.hourAgo {
  line-height: 25px;
  display: inline-block;
  min-width: 95px;
}
.firstIconShape {
  height: 44.7px;
  width: 46px;
}
.frameInput {
  margin: 0;
  height: 42.4px;
  width: 42px;
}
.thirdIconShape {
  height: 49.8px;
  width: 38.3px;
  position: relative;
  cursor: pointer;
  z-index: 1;
}
.firstIconShapeParent {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.firstDot,
.secondDot,
.thirdDot {
  position: relative;
  line-height: 25px;
  display: inline-block;
  min-width: 27px;
  z-index: 1;
}
.secondDot,
.thirdDot {
  min-width: 18px;
}
.thirdDot {
  min-width: 27px;
}
.firstDotParent,
.frameContainer {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.firstDotParent {
  flex: 1;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.frameContainer {
  align-self: stretch;
  justify-content: flex-end;
  padding: 0 var(--padding-10xs) 0 var(--padding-2xs);
}
.frameParent,
.frameWrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.frameParent {
  align-self: stretch;
  align-items: flex-end;
  gap: var(--gap-2xs-9);
}
.frameWrapper {
  width: 211.3px;
  align-items: flex-start;
  padding: var(--padding-10xs-3) 0 0;
  box-sizing: border-box;
  font-size: var(--font-size-base);
}
.hourAgoParent,
.infoTitleInner {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  max-width: 100%;
}
.hourAgoParent {
  flex: 1;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.infoTitleInner {
  align-self: stretch;
  justify-content: flex-start;
  padding: 0 var(--padding-22xl) 0 var(--padding-17xl);
  box-sizing: border-box;
  text-align: center;
  font-size: var(--font-size-xl);
  color: var(--color-white);
  font-family: var(--font-roboto);
}
@media screen and (max-width: 800px) {
  .hourAgoParent {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 450px) {
  .hourAgo {
    font-size: var(--font-size-base);
    line-height: 20px;
  }
  .firstIconShapeParent {
    flex-wrap: wrap;
  }
  .secondDot {
    width: 100%;
    height: 18px;
  }
  .firstDotParent {
    flex-wrap: wrap;
  }
}
