.componentChild {
  align-self: stretch;
  flex: 1;
  position: relative;
  border-radius: var(--br-31xl);
  background-color: var(--color-white);
  max-width: 100%;
}
.rectangleWrapper {
  height: 100%;
  width: 100%;
  margin: 0 !important;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
  z-index: 4;
}
