.image9Icon {
  width: 1440px;
  height: 638px;
  position: absolute;
  margin: 0 !important;
  top: 1px;
  right: -6px;
  object-fit: cover;
  mix-blend-mode: luminosity;
}
.saveStatusWrapper {
  width: 1267px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl);
  box-sizing: border-box;
  max-width: 100%;
}
.span {
  color: var(--color-white);
}
.nerd {
  color: var(--color-red);
}
.nestNerdTxtContainer {
  width: 100%;
}
.nestNerd {
  margin: 0;
  width: 290.5px;
  position: relative;
  font-size: inherit;
  line-height: 20px;
  font-weight: 400;
  font-family: inherit;
  display: flex;
  align-items: center;
  z-index: 2;
}
.editprofile2,
.nestNerdParent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
}
.nestNerdParent {
  align-self: stretch;
  background-color: var(--color-dimgray-500);
  padding: var(--padding-44xl) var(--padding-69xl) var(--padding-140xl);
  gap: var(--gap-30xl);
  flex-shrink: 0;
  max-width: 102%;
  z-index: 1;
  text-align: left;
  font-size: var(--font-size-29xl);
  color: var(--color-black);
  font-family: var(--font-secular-one);
}
.editprofile2 {
  width: 100%;
  height: 1024px;
  position: relative;
  background-color: var(--color-darkslateblue);
  overflow: hidden;
  padding: 0 var(--padding-12xs) var(--padding-494xl);
  gap: 111px;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1125px) {
  .editprofile2 {
    height: auto;
  }
}
@media screen and (max-width: 800px) {
  .nestNerd {
    font-size: var(--font-size-19xl);
    line-height: 16px;
  }
  .nestNerdParent {
    gap: var(--gap-5xl);
    padding: var(--padding-22xl) var(--padding-25xl) 103px;
    box-sizing: border-box;
  }
  .editprofile2 {
    gap: var(--gap-36xl);
  }
}
@media screen and (max-width: 450px) {
  .nestNerd {
    font-size: var(--font-size-10xl);
    line-height: 12px;
  }
  .nestNerdParent {
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
  .editprofile2 {
    gap: var(--gap-9xl);
  }
}
