.erangelContainerIcon {
  align-self: stretch;
  height: 291px;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
  z-index: 2;
}
.erangelPetaBesar,
.erangle {
  position: relative;
  line-height: 25px;
}
.erangelPetaBesar {
  align-self: stretch;
  height: 268px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  z-index: 3;
}
.erangle {
  margin: 0;
  font-size: inherit;
  font-weight: 800;
  font-family: inherit;
  z-index: 2;
}
.erangelTitleContainer {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl) 0 var(--padding-3xl);
  margin-top: -231px;
  font-size: var(--font-size-17xl);
  color: var(--color-olive);
}
.erangelDescription,
.erangelWrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.erangelWrapper {
  align-self: stretch;
  align-items: flex-end;
  min-height: 268px;
  margin-top: -17px;
}
.erangelDescription {
  align-items: flex-start;
  padding: 0 var(--padding-8xs) 0 0;
  box-sizing: border-box;
  max-width: 100%;
  text-align: center;
  font-size: var(--font-size-5xl);
  color: var(--color-darkslateblue);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 800px) {
  .erangle {
    font-size: var(--font-size-10xl);
    line-height: 20px;
  }
}
@media screen and (max-width: 450px) {
  .erangelPetaBesar {
    font-size: var(--font-size-lgi);
    line-height: 20px;
  }
  .erangle {
    font-size: var(--font-size-3xl);
    line-height: 15px;
  }
}
