}
.nerd {
  color: var(--color-red);
}
.nestNerdTxtContainer {
  width: 100%;
}
.nestNerd {
  margin: 0;
  width: 290.5px;
  position: relative;
  font-size: inherit;
  line-height: 20px;
  font-weight: 400;
  font-family: inherit;
  display: flex;
  align-items: center;
  z-index: 2;
}
.webIniMenawarkan {
