  justify-content: flex-start;
  padding: var(--padding-6xs) var(--padding-lg) 0 0;
  box-sizing: border-box;
  max-width: 100%;
}
.frameInner,
.service {
  position: relative;
  z-index: 2;
}
.service {
  align-self: stretch;
  letter-spacing: 0.02em;
}
.frameInner {
  height: 1px;
  flex: 1;
  background-color: var(--color-white);
  border-top: 1px solid var(--color-black);
  box-sizing: border-box;
}
.lineWrapper,
.serviceParent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.lineWrapper {
  width: 77.6px;
  flex-direction: row;
  padding: 0 var(--padding-6xs) 0 var(--padding-5xs);
  box-sizing: border-box;
}
.serviceParent {
  align-self: stretch;
  flex-direction: column;
  padding: 0 0 var(--padding-5xs);
  gap: var(--gap-3xs);
}
.games {
  flex: 1;
  position: relative;
  letter-spacing: 0.02em;
  flex-shrink: 0;
  z-index: 2;
}
.gamesWrapper {
  width: 90.3px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 var(--padding-9xs);
  box-sizing: border-box;
}
.announced,
.solution {
  position: relative;
  letter-spacing: 0.02em;
  display: inline-block;
  min-width: 102px;
  z-index: 2;
}
.solution {
  min-width: 79px;
}
.solutionTitle,
.solutionTitleWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.solutionTitle {
  align-self: stretch;
  gap: var(--gap-mid);
}
.solutionTitleWrapper {
  width: 153px;
  padding: var(--padding-6xs) var(--padding-7xl) 0 0;
  box-sizing: border-box;
}
.information,
.lineDiv {
  position: relative;
  z-index: 2;
}
.information {
  letter-spacing: 0.02em;
  display: inline-block;
  min-width: 113px;
}
.lineDiv {
  height: 1px;
  flex: 1;
  border-top: 1px solid var(--color-black);
  box-sizing: border-box;
}
.informationParent,
.lineContainer {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.lineContainer {
  width: 65.7px;
  flex-direction: row;
  padding: 0 var(--padding-12xs) 0 var(--padding-11xs);
  box-sizing: border-box;
}
.informationParent {
  flex-direction: column;
  gap: var(--gap-3xs);
}
.nestnedrgmailcom,
.xxxxx {
