  width: 91.4px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-mid);
}
.personalPolicy,
.termconditions {
  position: relative;
  letter-spacing: 0.02em;
  z-index: 2;
}
.personalPolicy {
  width: 165.3px;
  display: inline-block;
}
