  align-self: stretch;
}
.personalPolicyParent {
  align-self: stretch;
  flex-direction: column;
  gap: var(--gap-lg);
}
.frameContainer,
.infoContainer,
.personalPolicyParent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.frameContainer {
  width: 185.8px;
  flex-direction: column;
  gap: var(--gap-6xl);
