}
.instagramIcon {
  width: 31px;
  height: 32px;
  position: relative;
  object-fit: cover;
}
.instagramWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-11xs-5) 0 0;
}
.facebookIcon {
  width: 29px;
  height: 31px;
  position: relative;
  object-fit: cover;
}
.socialMediaIcon {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-11xs) 0 0;
}
.twitterxIcon,
.whatsappIcon {
  position: relative;
  object-fit: cover;
}
.whatsappIcon {
  height: 35px;
  width: 31px;
  min-height: 35px;
}
.twitterxIcon {
  width: 25px;
  height: 25px;
}
.socialMediaIcon1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-8xs) 0 0;
}
.socialContainer,
.socialContainerWrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.socialContainer {
  width: 266px;
  justify-content: space-between;
  gap: var(--gap-xl);
  z-index: 2;
}
.socialContainerWrapper {
  width: 1206.8px;
