  max-width: 100%;
}
.frameParent,
.infoContainerParent {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  gap: var(--gap-46xl);
  max-width: 100%;
  font-size: var(--font-size-xl);
  font-family: var(--font-nunito);
}
.frameParent {
  align-self: stretch;
  background-color: var(--color-dimgray-500);
  align-items: flex-start;
  padding: var(--padding-44xl) var(--padding-69xl) var(--padding-121xl);
  box-sizing: border-box;
  position: relative;
  gap: var(--gap-30xl);
  max-width: 102%;
  flex-shrink: 0;
  text-align: left;
  font-size: var(--font-size-29xl);
  color: var(--color-black);
  font-family: var(--font-secular-one);
}
@media screen and (max-width: 1350px) {
  .infoContainer {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 800px) {
  .nestNerd {
    font-size: var(--font-size-19xl);
    line-height: 16px;
  }
  .infoContainer {
    gap: var(--gap-19xl);
  }
  .infoContainerParent {
    gap: var(--gap-13xl);
  }
  .frameParent {
    gap: var(--gap-5xl);
    padding: var(--padding-22xl) var(--padding-25xl) var(--padding-72xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .nestNerd {
    font-size: var(--font-size-10xl);
    line-height: 12px;
  }
  .announced,
  .cilandakjakartaSelatan,
  .games,
  .information,
  .legality,
  .nestnedrgmailcom,
  .personalPolicy,
  .service,
  .solution,
  .termconditions,
  .webIniMenawarkan,
  .xxxxx {
    font-size: var(--font-size-base);
  }
  .infoContainer {
    gap: var(--gap-lgi);
  }
  .infoContainerParent {
    gap: var(--gap-base);
  }
  .frameParent {
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
}
