.home {
  text-decoration: none;
  position: relative;
  line-height: 20px;
  color: inherit;
  display: inline-block;
  min-width: 65px;
}
.homeCommunity {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) 0 0;
}
.comunity {
  position: relative;
  line-height: 20px;
  display: inline-block;
  min-width: 102px;
  cursor: pointer;
}
.homeCommunity1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) 0 0;
}
.news {
  text-decoration: none;
  position: relative;
  line-height: 20px;
  color: inherit;
  display: inline-block;
  min-width: 61px;
  cursor: pointer;
}
.newsForumButton {
  width: 85px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) var(--padding-xl) 0 0;
  box-sizing: border-box;
}
.forum {
  text-decoration: none;
  position: relative;
  line-height: 20px;
  color: inherit;
  display: inline-block;
  min-width: 70px;
  cursor: pointer;
}
.newsForumButton1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lg) 0 0;
}
.games1 {
  position: relative;
  line-height: 150%;
  display: inline-block;
  min-width: 76px;
}
.frameChild {
  width: 29.9px;
  height: 19px;
  position: relative;
  border-radius: var(--br-12xs);
}
.games,
.gamesInner,
.newsForumButtonParent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.gamesInner {
  flex-direction: column;
  padding: var(--padding-4xs-5) 0 0;
}
.games,
.newsForumButtonParent {
  flex-direction: row;
}
.games {
  width: 235px;
  border-radius: var(--br-5xs);
  padding: var(--padding-3xs);
  box-sizing: border-box;
  gap: var(--gap-3xs);
}
.newsForumButtonParent {
  gap: var(--gap-23xl);
  max-width: 100%;
}
.thoughtBox {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-11xl);
  background-color: var(--color-white);
  width: 100%;
  height: 100%;
}
.whatAreYou {
  position: absolute;
  top: 8px;
  left: 126px;
  z-index: 1;
}
.thoughtBoxParent {
  height: 58px;
  width: 619px;
  position: relative;
  max-width: 100%;
  cursor: pointer;
}
.frameGroup,
.frameWrapper {
  display: flex;
  align-items: flex-start;
  max-width: 100%;
}
.frameWrapper {
  align-self: stretch;
  flex-direction: row;
  justify-content: flex-end;
  font-size: var(--font-size-13xl);
  color: var(--color-darkslategray-200);
  font-family: var(--font-average-sans);
}
.frameGroup {
  flex: 1;
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-18xl);
}
.vectorIcon {
  align-self: stretch;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.vectorWrapper {
  height: 54px;
  width: 59px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 var(--padding-8xs);
  box-sizing: border-box;
}
.frameParent,
.homeCommunityParent {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  max-width: 100%;
}
.frameParent {
  width: 773px;
  align-items: flex-end;
  gap: var(--gap-33xl);
}
.homeCommunityParent {
  align-items: flex-start;
  gap: var(--gap-44xl);
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-white);
  font-family: var(--font-roboto);
}
@media screen and (max-width: 1200px) {
  .homeCommunityParent {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 1050px) {
  .homeCommunityParent {
    gap: var(--gap-12xl);
  }
}
@media screen and (max-width: 750px) {
  .newsForumButtonParent {
    flex-wrap: wrap;
    gap: var(--gap-2xl);
  }
  .whatAreYou {
    font-size: var(--font-size-7xl);
  }
  .frameGroup {
    gap: var(--gap-lg);
    min-width: 100%;
  }
  .frameParent {
    flex-wrap: wrap;
  }
  .homeCommunityParent {
    gap: var(--gap-base);
  }
}
@media screen and (max-width: 450px) {
  .comunity,
  .forum,
  .home,
  .news {
    font-size: var(--font-size-lgi);
    line-height: 16px;
  }
  .games1 {
    line-height: 29px;
  }
  .games1,
  .whatAreYou {
    font-size: var(--font-size-lgi);
  }
  .frameParent {
    gap: var(--gap-7xl);
  }
}
