.footerRectIcon {
  align-self: stretch;
  height: 569px;
  border-radius: var(--br-3xs);
  max-width: 100%;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.footerRectIcon,
.minutesAgo,
.vectorIcon {
  position: relative;
  z-index: 2;
}
.minutesAgo {
  line-height: 25px;
}
.vectorIcon {
  height: 42.5px;
  width: 46px;
}
.frameInput {
  margin: 0;
  height: 43.3px;
  width: 42px;
}
.vectorIcon1 {
  height: 47.3px;
  width: 38.3px;
  position: relative;
  cursor: pointer;
  z-index: 2;
}
.vectorParent {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.timeContent,
.timeContentInner {
  display: flex;
  align-items: flex-start;
}
.timeContentInner {
  width: 211.3px;
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-lgi) 0 0;
  box-sizing: border-box;
}
.timeContent {
  flex: 1;
  flex-direction: row;
  justify-content: space-between;
  max-width: 100%;
  gap: var(--gap-xl);
}
.timeInfo {
  width: 1019.3px;
  flex-direction: row;
  padding: 0 var(--padding-12xl);
  box-sizing: border-box;
}
.footerContent,
.postFooter,
.timeInfo {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.footerContent {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-2xl);
}
.postFooter {
  align-self: stretch;
  flex-direction: row;
  padding: 0 var(--padding-6xs) 0 var(--padding-mid);
  box-sizing: border-box;
  text-align: center;
  font-size: var(--font-size-xl);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 750px) {
  .timeContent {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 450px) {
  .minutesAgo {
    font-size: var(--font-size-base);
    line-height: 20px;
  }
  .vectorParent {
    flex-wrap: wrap;
    justify-content: center;
  }
}
