.newsInner {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-58xl);
  box-sizing: border-box;
  max-width: 100%;
}
.mobileLegendsImage {
  align-self: stretch;
  flex: 1;
  position: relative;
  border-radius: var(--br-5xs);
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.mobileLegendsImageWrapper {
  height: 310px;
  width: 328px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 var(--padding-lgi);
  box-sizing: border-box;
  min-width: 328px;
  max-width: 100%;
}
.mobileLegendsIcon {
  border: 0;
  background-color: var(--color-gainsboro-300);
  height: 250px;
  width: 100%;
  outline: 0;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  left: 0;
  border-radius: var(--br-3xs);
  mix-blend-mode: hue;
}
.mobileLegendsMengumumkan {
  width: 492px;
  height: 268px;
  position: relative;
  line-height: 25px;
  display: flex;
  align-items: center;
  flex-shrink: 0;
  max-width: 100%;
  z-index: 2;
}
.mobileLegend {
  position: relative;
  font-size: var(--font-size-17xl);
  line-height: 25px;
  font-weight: 700;
  font-family: inherit;
  color: var(--color-gold-100);
  text-align: center;
  z-index: 1;
  margin: -256px 0 0;
}
.frameGroup,
.mobileLegendsIconParent {
  display: flex;
  justify-content: flex-start;
  max-width: 100%;
}
.mobileLegendsIconParent {
  flex: 1;
  flex-direction: column;
  align-items: flex-start;
  padding: var(--padding-lg) var(--padding-lg) 0;
  box-sizing: border-box;
  position: relative;
  min-width: 353px;
  min-height: 286px;
}
.frameGroup {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-end;
  gap: var(--gap-18xl);
}
.pUBGImage {
  width: 329px;
  position: relative;
  border-radius: var(--br-5xs);
  max-height: 100%;
  object-fit: cover;
  max-width: 100%;
}
.image1Icon,
.pUBGIcon {
  position: absolute;
  margin: 0 !important;
}
.pUBGIcon {
  width: 100%;
  height: 250px;
  top: 0;
  right: 0;
  left: 0;
  border-radius: var(--br-3xs);
  background-color: #2b2b2b;
  mix-blend-mode: normal;
  z-index: 1;
}
.image1Icon {
  height: 508px;
  width: 1221px;
  right: -754px;
  bottom: -177px;
  object-fit: contain;
  mix-blend-mode: exclusion;
}
.image1Parent,
.pubgMengumumkanPembaruan {
  display: flex;
  position: relative;
  max-width: 100%;
}
.pubgMengumumkanPembaruan {
  height: 268px;
  flex: 1;
  line-height: 25px;
  align-items: center;
  z-index: 2;
}
.image1Parent {
  width: 493px;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.pubg {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 25px;
  font-weight: 700;
  font-family: inherit;
  display: inline-block;
  min-width: 110px;
  z-index: 3;
}
.pUBGIconParent,
.pubgWrapper {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.pubgWrapper {
  flex-direction: row;
  padding: 0 var(--padding-12xs);
  margin-top: -245px;
  text-align: center;
  font-size: var(--font-size-17xl);
  color: var(--color-gold-100);
}
.pUBGIconParent {
  flex: 1;
  flex-direction: column;
  padding: var(--padding-10xs) var(--padding-mid) 0;
  box-sizing: border-box;
  position: relative;
  min-width: 353px;
  min-height: 271px;
  max-width: 100%;
}
.pUBGImageParent {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-end;
  gap: var(--gap-20xl);
  max-width: 100%;
}
.frameParent,
.news,
.newsChild,
.pUBGImageParent {
  display: flex;
  justify-content: flex-start;
}
.frameParent {
  flex: 1;
  flex-direction: column;
  align-items: flex-end;
  gap: 67px;
  max-width: 100%;
}
.news,
.newsChild {
  box-sizing: border-box;
  align-items: flex-start;
}
.newsChild {
  width: 1065px;
  flex-direction: row;
  padding: 0 var(--padding-58xl) var(--padding-19xl);
  max-width: 100%;
  flex-shrink: 0;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
.news {
  width: 100%;
  height: 2424px;
  position: relative;
  background-color: var(--color-darkslateblue);
  border: 1px solid var(--color-black);
  overflow: hidden;
  flex-direction: column;
  padding: var(--padding-23xl) 0 593px;
  gap: 123px;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1125px) {
  .newsInner {
    padding-left: var(--padding-19xl);
    padding-right: var(--padding-19xl);
    box-sizing: border-box;
  }
  .frameParent {
    gap: var(--gap-14xl);
  }
  .newsChild {
    padding-left: var(--padding-19xl);
    padding-right: var(--padding-19xl);
    box-sizing: border-box;
  }
  .news {
    height: auto;
  }
}
@media screen and (max-width: 825px) {
  .mobileLegendsImage {
    align-self: stretch;
    width: auto;
  }
  .mobileLegendsImageWrapper {
    flex: 1;
  }
  .mobileLegend {
    font-size: var(--font-size-10xl);
    line-height: 20px;
  }
  .mobileLegendsIconParent {
    min-width: 100%;
  }
  .frameGroup {
    flex-wrap: wrap;
  }
  .pUBGImage {
    flex: 1;
  }
  .pubg {
    font-size: var(--font-size-10xl);
    line-height: 20px;
  }
  .pUBGIconParent {
    min-width: 100%;
  }
  .pUBGImageParent {
    flex-wrap: wrap;
  }
  .news {
    gap: 61px;
  }
}
@media screen and (max-width: 450px) {
  .mobileLegendsMengumumkan {
    font-size: var(--font-size-lgi);
    line-height: 20px;
  }
  .mobileLegend {
    font-size: var(--font-size-3xl);
    line-height: 15px;
  }
  .frameGroup {
    gap: var(--gap-lg);
  }
  .pubgMengumumkanPembaruan {
    font-size: var(--font-size-lgi);
    line-height: 20px;
  }
  .pubg {
    font-size: var(--font-size-3xl);
    line-height: 15px;
  }
  .pUBGImageParent {
    gap: var(--gap-lgi);
  }
  .frameParent {
    gap: var(--gap-mid);
  }
  .news {
    gap: var(--gap-12xl);
  }
}
