.ellipseIcon,
.kimJiwon {
  position: relative;
  z-index: 1;
}
.ellipseIcon {
  height: 70px;
  width: 69px;
  border-radius: 50%;
  object-fit: cover;
}
.kimJiwon {
  margin: 0;
  font-size: inherit;
  line-height: 25px;
  font-weight: 500;
  font-family: inherit;
}
.kimJiwonWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 var(--padding-3xl);
}
.ellipseParent {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-start;
  gap: var(--gap-9xl);
  text-align: center;
  font-size: var(--font-size-13xl);
  color: var(--color-white);
  font-family: var(--font-roboto);
}
@media screen and (max-width: 800px) {
  .kimJiwon {
    font-size: var(--font-size-7xl);
    line-height: 20px;
  }
}
@media screen and (max-width: 450px) {
  .kimJiwon {
    font-size: var(--font-size-lgi);
    line-height: 15px;
  }
}
