.nerd {
  color: var(--color-red);
}
.nestNerd {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 20px;
  font-weight: 400;
  font-family: inherit;
}
.nestNerdWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 var(--padding-xl);
}
.all {
  text-decoration: none;
  position: relative;
  color: inherit;
  display: inline-block;
  min-width: 22px;
}
.frameChild {
  width: 18px;
  height: 13px;
  position: relative;
}
.polygonWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-6xs-5) 0 0;
}
.allParent,
.frameItem {
  background-color: var(--color-gainsboro-300);
}
.allParent {
  width: 100px;
  border-radius: var(--br-8xs) 0 0 var(--br-8xs);
  border-right: 1px solid var(--color-black);
  box-sizing: border-box;
  overflow: hidden;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-6xs) var(--padding-5xl);
  gap: var(--gap-3xs);
  z-index: 2;
}
.frameItem {
  align-self: stretch;
  width: 469.7px;
  position: relative;
  border-radius: var(--br-3xs);
  display: none;
  max-width: 100%;
}
.vectorIcon {
  position: absolute;
  top: 7px;
  left: 431.7px;
  width: 25px;
  height: 27px;
  z-index: 1;
}
.frameInner {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-3xs);
  background-color: var(--color-gainsboro-300);
  width: 100%;
  height: 100%;
  display: none;
}
.rectangleGroup,
.vectorIcon1 {
  position: absolute;
  top: 7px;
  left: 430.7px;
  width: 25px;
  height: 27px;
  z-index: 1;
}
.rectangleGroup {
  top: 0;
  left: 0;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: var(--br-3xs);
  background-color: var(--color-gainsboro-300);
  width: 100%;
  height: 100%;
  z-index: 3;
}
.vectorParent {
  margin-left: -1px;
  height: 40px;
  flex: 1;
  position: relative;
  flex-shrink: 0;
  max-width: 100%;
}
.rectangleParent {
  flex: 1;
  border-radius: var(--br-3xs);
  background-color: var(--color-gainsboro-300);
  flex-direction: row;
  justify-content: flex-start;
  margin-left: -71.7px;
}
.frameParent,
.nestedPanelsInner,
.rectangleParent {
  display: flex;
  align-items: flex-start;
  max-width: 100%;
}
.frameParent {
  align-self: stretch;
  flex-direction: row;
  justify-content: flex-start;
}
.nestedPanelsInner {
  width: 498px;
  flex-direction: column;
  justify-content: flex-end;
  padding: 0 0 var(--padding-2xs);
  box-sizing: border-box;
}
.chatBubbleIcon {
  height: 59px;
  width: 55px;
  position: relative;
  object-fit: cover;
  cursor: pointer;
}
.ellipseIcon,
.ellipseIcon1 {
  border-radius: 50%;
  object-fit: cover;
}
.ellipseIcon {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.ellipseIcon1 {
  height: 67px;
  width: 66px;
  position: relative;
  z-index: 4;
}
.ellipseParent,
.nestedPanels,
.sidePanel {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.ellipseParent {
  align-items: flex-start;
  position: relative;
}
.nestedPanels,
.sidePanel {
  align-items: flex-end;
  gap: var(--gap-25xl-5);
  max-width: 100%;
  font-size: var(--font-size-xl);
  color: var(--color-black);
  font-family: var(--font-average-sans);
}
.sidePanel {
  width: 1346px;
  justify-content: space-between;
  padding: 0 var(--padding-xl) var(--padding-85xl) 0;
  box-sizing: border-box;
  gap: var(--gap-xl);
  text-align: left;
  font-size: var(--font-size-29xl);
  color: var(--color-white);
  font-family: var(--font-secular-one);
}
@media screen and (max-width: 1050px) {
  .sidePanel {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 750px) {
  .nestNerd {
    font-size: var(--font-size-19xl);
    line-height: 16px;
  }
  .nestedPanels {
    flex-wrap: wrap;
    gap: var(--gap-3xl);
  }
}
@media screen and (max-width: 450px) {
  .nestNerd {
    font-size: var(--font-size-10xl);
    line-height: 12px;
  }
  .all {
    font-size: var(--font-size-base);
  }
}
