.skinUpdate {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: -1px;
  bottom: 0;
  left: 0;
  background-color: var(--color-gray-100);
  mix-blend-mode: hard-light;
}
.skinImageIcon {
  height: 291px;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
  z-index: 1;
}
.skinImageContainer {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 0 var(--padding-3xs);
  box-sizing: border-box;
  max-width: 100%;
}
.newSkin {
  position: relative;
  line-height: 25px;
  font-weight: 800;
  z-index: 1;
}
.skinInfo {
  width: 339px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-xl);
  max-width: 100%;
  text-align: center;
}
.anniversaryImageContainer {
  align-self: stretch;
  height: 291px;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
  z-index: 1;
}
.weaponAnniversarySkin {
  margin-block-start: 0;
  margin-block-end: 10px;
}
.availableNow {
  margin: 0;
}
.weaponAnniversarySkinContainer {
  position: relative;
  line-height: 25px;
  font-weight: 800;
  z-index: 1;
}
.anniversarySkin {
  width: 329px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-mini);
  max-width: 100%;
}
.updatesDividerBottom {
  height: 74.7px;
  width: 43px;
  position: absolute;
  margin: 0 !important;
  top: 167.6px;
  right: 58px;
  flex-shrink: 0;
  object-fit: contain;
  z-index: 1;
}
.updateItems,
.updatesContent {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
  max-width: 100%;
}
.updateItems {
  flex: 1;
  overflow-x: auto;
  padding: var(--padding-38xl) var(--padding-59xl) var(--padding-27xl);
  position: relative;
  gap: var(--gap-76xl-5);
}
.updatesContent {
  align-self: stretch;
  padding: 0 0 0 var(--padding-10xs);
  flex-shrink: 0;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 1350px) {
  .updateItems {
    padding-left: var(--padding-20xl);
    padding-right: var(--padding-20xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 800px) {
  .updateItems {
    gap: var(--gap-29xl);
    padding-top: var(--padding-18xl);
    padding-bottom: var(--padding-11xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .newSkin,
  .weaponAnniversarySkinContainer {
    font-size: var(--font-size-lgi);
    line-height: 20px;
  }
  .updateItems {
    gap: var(--gap-5xl);
  }
}
