.nerd {
  color: var(--color-red);
}
.nestNerd {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 20px;
  font-weight: 400;
  font-family: inherit;
  cursor: pointer;
}
.nestNerdWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-mid) 0 0;
}
.frameChild {
  height: 40px;
  width: 469.7px;
  position: relative;
  border-radius: var(--br-3xs);
  background-color: var(--color-gainsboro-300);
  display: none;
  max-width: 100%;
}
.vectorIcon {
  height: 27px;
  width: 25px;
  position: relative;
  z-index: 1;
}
.frameWrapper,
.rectangleParent {
  display: flex;
  align-items: flex-start;
  box-sizing: border-box;
  max-width: 100%;
}
.rectangleParent {
  align-self: stretch;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: var(--br-3xs);
  background-color: var(--color-gainsboro-300);
  flex-direction: row;
  justify-content: flex-end;
  padding: var(--padding-6xs) var(--padding-sm) var(--padding-7xs);
  flex-shrink: 0;
}
.frameWrapper {
  flex: 1;
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-smi) 0 0;
  min-width: 305px;
}
.chatBubbleIcon {
  width: 55px;
  height: 59px;
  position: relative;
  object-fit: cover;
  cursor: pointer;
}
.chatBubbleWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-9xs) 0 0;
}
.ellipseIcon {
  height: 67px;
  width: 66px;
  position: relative;
  border-radius: 50%;
  object-fit: cover;
  cursor: pointer;
}
.frameContainer,
.frameGroup,
.frameParent {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.frameContainer {
  width: 165px;
  justify-content: space-between;
  flex-shrink: 0;
  gap: var(--gap-xl);
}
.frameGroup,
.frameParent {
  max-width: 100%;
}
.frameGroup {
  width: 694px;
  justify-content: flex-start;
  gap: var(--gap-40xl-3);
}
.frameParent {
  align-self: stretch;
  justify-content: space-between;
  gap: var(--gap-xl);
  text-align: left;
  font-size: var(--font-size-29xl);
  color: var(--color-white);
  font-family: var(--font-secular-one);
}
@media screen and (max-width: 1200px) {
  .frameParent {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 825px) {
  .nestNerd {
    font-size: var(--font-size-19xl);
    line-height: 16px;
  }
  .frameGroup {
    flex-wrap: wrap;
    gap: var(--gap-11xl);
  }
}
@media screen and (max-width: 450px) {
  .nestNerd {
    font-size: var(--font-size-10xl);
    line-height: 12px;
  }
}
