.frameChild {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--color-gray-100);
  mix-blend-mode: hard-light;
}
.anniversarySkinBackground {
  align-self: stretch;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
  z-index: 1;
}
.weaponAnniversarySkin {
  margin-block-start: 0;
  margin-block-end: 10px;
}
.availableNow {
  margin: 0;
}
.weaponAnniversarySkinContainer {
  position: relative;
  line-height: 25px;
  font-weight: 800;
  z-index: 1;
}
.anniversarySkinBackgroundParent {
  align-self: stretch;
  width: 329px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-mini);
  max-width: 100%;
}
.vectorIcon {
  height: 74.7px;
  width: 43px;
  position: absolute;
  margin: 0 !important;
  top: 167.6px;
  right: 51px;
  flex-shrink: 0;
  object-fit: contain;
  z-index: 1;
}
.rectangleParent,
.vahicles {
  position: relative;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
}
.rectangleParent {
  margin-left: -5px;
  width: 1438px;
  height: 469px;
  overflow-x: auto;
  flex-shrink: 0;
  flex-direction: row;
  padding: var(--padding-38xl) var(--padding-69xl) var(--padding-27xl);
  gap: var(--gap-76xl-5);
  max-width: 100%;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-white);
  font-family: var(--font-roboto);
}
.vahicles {
  width: 100%;
  height: 3014px;
  background-color: var(--color-darkslateblue);
  overflow: hidden;
  flex-direction: column;
  padding: var(--padding-22xl) 0 var(--padding-461xl);
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1350px) {
  .rectangleParent {
    padding-left: var(--padding-25xl);
    padding-right: var(--padding-25xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1125px) {
  .vahicles {
    height: auto;
  }
}
@media screen and (max-width: 800px) {
  .rectangleParent {
    gap: var(--gap-29xl);
    padding: var(--padding-18xl) var(--padding-3xl) var(--padding-11xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .weaponAnniversarySkinContainer {
    font-size: var(--font-size-lgi);
    line-height: 20px;
  }
  .rectangleParent {
    gap: var(--gap-5xl);
  }
}
