.gameInfo {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 25px;
  font-weight: 700;
  font-family: inherit;
  z-index: 1;
}
.gameInfoWrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-54xl);
  box-sizing: border-box;
  max-width: 100%;
}
.buttonIcons {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-5xs);
  background-color: var(--color-white);
}
.maps {
  position: relative;
  line-height: 25px;
  font-weight: 500;
  display: inline-block;
  min-width: 66px;
  z-index: 1;
}
.buttonIconsParent {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-base) var(--padding-36xl);
  position: relative;
}
.frameChild {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-5xs);
  background-color: var(--color-white);
}
.weapon {
  position: relative;
  line-height: 25px;
  font-weight: 500;
  display: inline-block;
  min-width: 103px;
  z-index: 1;
}
.rectangleParent {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-base) var(--padding-18xl);
  position: relative;
  cursor: pointer;
  color: var(--color-dimgray-400);
}
.frameItem {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-5xs);
  background-color: var(--color-gold-100);
}
.vehicles {
  position: relative;
  line-height: 25px;
  font-weight: 500;
  display: inline-block;
  min-width: 101px;
  z-index: 1;
}
.rectangleGroup {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-base) var(--padding-19xl);
  position: relative;
}
.eventButtonIcons {
  height: 100%;
  width: 177px;
  position: absolute;
  margin: 0 !important;
  top: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-5xs);
  background-color: var(--color-white);
}
.arcade {
  position: relative;
  line-height: 25px;
  font-weight: 500;
  display: inline-block;
  min-width: 87px;
  z-index: 1;
}
.eventButtonIcons1 {
  height: 100%;
  width: 177px;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  border-radius: var(--br-5xs);
  background-color: var(--color-white);
  cursor: pointer;
}
.event {
  position: relative;
  line-height: 25px;
  font-weight: 500;
  display: inline-block;
  min-width: 70px;
  z-index: 1;
}
.button,
.buttonWrapper,
.eventButtonIconsParent {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  max-width: 100%;
}
.eventButtonIconsParent {
  flex: 1;
  justify-content: space-between;
  padding: var(--padding-base) var(--padding-35xl) var(--padding-base)
    var(--padding-26xl);
  box-sizing: border-box;
  position: relative;
  min-width: 157px;
  gap: var(--gap-xl);
  cursor: pointer;
}
.button,
.buttonWrapper {
  justify-content: center;
}
.button {
  width: 1011px;
  flex-wrap: wrap;
  gap: var(--gap-11xl);
  z-index: 1;
}
.buttonWrapper {
  width: 1197px;
  padding: 0 var(--padding-xl);
  box-sizing: border-box;
  font-size: var(--font-size-5xl);
  color: var(--color-black);
}
.frameInner {
  width: 100%;
  height: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--color-lightgray-100);
  mix-blend-mode: hard-light;
  z-index: 1;
}
.image2Icon,
.imageBorderIcon {
  position: absolute;
  object-fit: cover;
}
.image2Icon {
  top: 107px;
  left: -904px;
  width: 2318px;
  height: 1304px;
  mix-blend-mode: luminosity;
}
.imageBorderIcon {
  top: 0;
  left: 32px;
  border-radius: var(--br-5xs);
  width: 287px;
  height: 314px;
  z-index: 2;
}
.imageContainer {
  width: 1414px;
  height: 1411px;
  position: absolute;
  margin: 0 !important;
  top: -624px;
  left: 0;
}
.pubgPng21Icon,
.vectorIcon {
  object-fit: contain;
  z-index: 2;
}
.pubgPng21Icon {
  width: 400px;
  position: relative;
  max-height: 100%;
  max-width: 100%;
}
.vectorIcon {
  height: 74.7px;
  width: 43px;
  position: absolute;
  margin: 0 !important;
  top: 116.3px;
  right: 0;
}
.pubgCarPng1Icon {
  align-self: stretch;
  height: 218.5px;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: contain;
  z-index: 2;
}
.pubgCarPng1Wrapper {
  height: 265.6px;
  width: 415px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-mini) 0 0;
  box-sizing: border-box;
  max-width: 100%;
}
.pngwing1Icon {
  width: 295.5px;
  height: 300px;
  position: relative;
  object-fit: cover;
  z-index: 2;
}
.pngwing1Wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 var(--padding-lgi-6);
}
.car,
.pubgPng21Parent {
  position: relative;
  display: flex;
}
.pubgPng21Parent {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-start;
  gap: var(--gap-31xl);
  max-width: 100%;
}
.car {
  margin: 0;
  width: 57px;
  font-size: inherit;
  line-height: 25px;
  font-weight: 800;
  font-family: inherit;
  align-items: center;
  justify-content: center;
  min-width: 57px;
  z-index: 2;
}
.carWrapper {
  width: 115px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.atv,
.motocycle {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 25px;
  font-weight: 800;
  font-family: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
}
.atv {
  width: 69px;
  min-width: 69px;
}
.motocycle {
  width: 169px;
}
.frameGroup,
.frameWrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  max-width: 100%;
}
.frameGroup {
  width: 973px;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.frameWrapper {
  width: 1227px;
  justify-content: center;
}
.frameParent,
.rectangleContainer {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  box-sizing: border-box;
  max-width: 100%;
}
.rectangleContainer {
  align-items: flex-end;
  padding: var(--padding-116xl) var(--padding-40xl) var(--padding-71xl);
  position: relative;
  gap: var(--gap-38xl-4);
  font-size: var(--font-size-17xl);
  color: var(--color-olive);
  font-family: var(--font-roboto);
}
.frameParent {
  align-items: flex-start;
  padding: 0 0 var(--padding-124xl);
  gap: var(--gap-37xl-5);
  text-align: center;
  font-size: var(--font-size-29xl);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 1350px) {
  .pubgPng21Parent {
    flex-wrap: wrap;
    justify-content: center;
  }
}
@media screen and (max-width: 1125px) {
  .frameParent {
    padding-bottom: var(--padding-74xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 800px) {
  .gameInfo {
    font-size: var(--font-size-19xl);
    line-height: 20px;
  }
  .pubgPng21Parent {
    gap: var(--gap-6xl);
  }
  .atv,
  .car,
  .motocycle {
    font-size: var(--font-size-10xl);
    line-height: 20px;
  }
  .rectangleContainer {
    gap: var(--gap-10xl);
    padding: var(--padding-69xl) var(--padding-10xl) var(--padding-39xl);
    box-sizing: border-box;
  }
  .frameParent {
    gap: var(--gap-9xl);
    padding-bottom: var(--padding-41xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .gameInfo {
    font-size: var(--font-size-10xl);
    line-height: 15px;
  }
  .gameInfoWrapper {
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
  .arcade,
  .event,
  .maps,
  .vehicles,
  .weapon {
    font-size: var(--font-size-lgi);
    line-height: 20px;
  }
  .eventButtonIconsParent {
    flex-wrap: wrap;
    justify-content: center;
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
  .atv,
  .car,
  .motocycle {
    font-size: var(--font-size-3xl);
    line-height: 15px;
  }
  .frameGroup {
    flex-wrap: wrap;
  }
}
