.image4Icon {
  width: 1443px;
  height: 875px;
  position: absolute;
  margin: 0 !important;
  top: 658px;
  left: -3px;
  object-fit: cover;
  mix-blend-mode: exclusion;
}
.frameChild {
  height: 109px;
  width: 5px;
  position: relative;
  border-right: 5px solid var(--color-white);
  box-sizing: border-box;
  z-index: 1;
}
.gameCategoryContentChild {
  width: 100%;
  height: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--color-gray-500);
  z-index: 1;
}
.gameCategory {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 16px;
  font-weight: 400;
  font-family: inherit;
  z-index: 2;
}
.kategoriGame {
  height: 54px;
  flex: 1;
  position: relative;
  line-height: 25px;
  display: flex;
  align-items: center;
  z-index: 2;
}
.gameCategoryContent,
.kategoriGameWrapper {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.kategoriGameWrapper {
  width: 126px;
  flex-direction: row;
  padding: 0 var(--padding-5xs);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--font-size-xl);
  color: var(--color-white);
  font-family: var(--font-roboto);
}
.gameCategoryContent {
  align-self: stretch;
  flex-direction: column;
  padding: var(--padding-base) var(--padding-7xs) 0;
  position: relative;
  gap: var(--gap-lg);
}
.gameCategoryContentWrapper {
  flex: 1;
  flex-direction: column;
  padding: var(--padding-11xs) 0 0;
  margin-left: -1px;
}
.gameCategoryContentWrapper,
.home,
.homeInner,
.lineParent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.lineParent {
  height: 106px;
  flex: 1;
  flex-direction: row;
}
.home,
.homeInner {
  box-sizing: border-box;
}
.homeInner {
  width: 438px;
  flex-direction: row;
  padding: 0 var(--padding-50xl) var(--padding-27xl);
  max-width: 100%;
}
.home {
  width: 100%;
  position: relative;
  background-color: var(--color-darkslateblue);
  overflow: hidden;
  flex-direction: column;
  padding: var(--padding-21xl) 0 0;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  font-size: var(--font-size-17xl);
  color: var(--color-gold-100);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 800px) {
  .gameCategory {
    font-size: var(--font-size-10xl);
    line-height: 13px;
  }
}
@media screen and (max-width: 450px) {
  .gameCategory {
    font-size: var(--font-size-3xl);
    line-height: 10px;
  }
  .kategoriGame {
    font-size: var(--font-size-base);
    line-height: 20px;
  }
  .homeInner {
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
}
