.image9Icon {
  width: 1440px;
  height: 638px;
  position: absolute;
  margin: 0 !important;
  top: 1px;
  right: -6px;
  object-fit: cover;
  mix-blend-mode: luminosity;
}
.username,
.vectorIcon {
  position: relative;
  z-index: 1;
}
.vectorIcon {
  width: 43px;
  height: 43px;
  cursor: pointer;
}
.username {
  font-size: var(--font-size-5xl);
  line-height: 16px;
  font-weight: 600;
  font-family: var(--font-montserrat);
  color: var(--color-gold-100);
  text-align: center;
  display: inline-block;
  min-width: 128px;
}
.username2Child {
  height: 55px;
  width: 501px;
  position: relative;
  border-radius: var(--br-3xs);
  display: none;
  max-width: 100%;
}
.boesok,
.username2 {
  display: flex;
  z-index: 1;
}
.boesok {
  width: 89px;
  border: 0;
  outline: 0;
  font-family: var(--font-montserrat);
  font-size: var(--font-size-5xl);
  background-color: transparent;
  height: 16px;
  position: relative;
  line-height: 16px;
  color: var(--color-black);
  text-align: center;
  align-items: center;
  justify-content: center;
  padding: 0;
}
.username2 {
  align-self: stretch;
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
  border: 0 solid var(--color-black);
  box-sizing: border-box;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lgi) var(--padding-5xs) var(--padding-xl);
  max-width: 100%;
}
.nickname {
  position: relative;
  font-size: var(--font-size-5xl);
  line-height: 16px;
  font-weight: 600;
  font-family: var(--font-montserrat);
  color: var(--color-gold-100);
  text-align: center;
  display: inline-block;
  min-width: 127px;
  z-index: 1;
}
.username2Item {
  height: 55px;
  width: 501px;
  position: relative;
  border-radius: var(--br-3xs);
  display: none;
  max-width: 100%;
}
.boesok14 {
  width: 127px;
  border: 0;
  outline: 0;
  font-family: var(--font-montserrat);
  font-size: var(--font-size-5xl);
  background-color: transparent;
  height: 16px;
  position: relative;
  line-height: 16px;
  color: var(--color-black);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  z-index: 1;
}
.username21,
.usernameInput {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.username21 {
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
  border: 0 solid var(--color-black);
  box-sizing: border-box;
  flex-direction: row;
  padding: var(--padding-lgi) var(--padding-5xs) var(--padding-xl);
  z-index: 1;
}
.usernameInput {
  flex-direction: column;
  gap: var(--gap-lg);
}
.save,
.saveButtonBackground {
  position: absolute;
  cursor: pointer;
}
.saveButtonBackground {
  top: 0;
  left: 0;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25) inset;
  border-radius: var(--br-3xs);
  background-color: var(--color-gold-100);
  width: 100%;
  height: 100%;
  z-index: 1;
}
.save {
  top: 19px;
  left: 48px;
  font-size: var(--font-size-13xl);
  line-height: 16px;
  font-weight: 600;
  font-family: var(--font-montserrat);
  color: var(--color-black);
  text-align: center;
  display: inline-block;
  min-width: 78px;
  z-index: 2;
}
.saveButtonBackgroundParent {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  height: 55px;
  flex: 1;
  position: relative;
}
.frameContainer {
  width: 184px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 var(--padding-8xs);
  box-sizing: border-box;
}
.usernameInputParent {
  margin: 0;
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  gap: var(--gap-44xl);
  max-width: 100%;
}
.accountInfo,
.frameParent,
.frameWrapper {
  display: flex;
  align-items: flex-start;
  max-width: 100%;
}
.frameWrapper {
  flex: 1;
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-14xl) 0 0;
  box-sizing: border-box;
  min-width: 326px;
}
.accountInfo,
.frameParent {
  flex-direction: row;
}
.frameParent {
  width: 835px;
  justify-content: flex-start;
  gap: var(--gap-43xl);
}
.accountInfo {
  align-self: stretch;
  justify-content: flex-end;
}
.editprofile,
.vectorParent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
}
.vectorParent {
  width: 1087px;
  padding: 0 var(--padding-xl) 0 0;
  gap: var(--gap-63xl);
  max-width: 100%;
}
.editprofile {
  width: 100%;
  height: 1024px;
  position: relative;
  background-color: var(--color-darkslateblue);
  overflow: hidden;
  padding: var(--padding-20xl) var(--padding-11xs) var(--padding-494xl)
    var(--padding-70xl);
  gap: var(--gap-139xl);
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1225px) {
  .vectorParent {
    gap: var(--gap-22xl);
  }
  .editprofile {
    gap: var(--gap-60xl);
    padding-left: var(--padding-25xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1050px) {
  .frameParent {
    gap: var(--gap-12xl);
  }
}
@media screen and (max-width: 750px) {
  .save {
    font-size: var(--font-size-7xl);
    line-height: 13px;
  }
  .usernameInputParent {
    gap: var(--gap-12xl);
  }
  .frameParent {
    flex-wrap: wrap;
  }
  .vectorParent {
    gap: var(--gap-xl);
  }
  .editprofile {
    height: auto;
    gap: var(--gap-20xl);
    padding-left: var(--padding-3xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .boesok,
  .boesok14,
  .nickname,
  .save,
  .username {
    font-size: var(--font-size-lgi);
    line-height: 13px;
  }
  .save {
    line-height: 10px;
  }
  .usernameInputParent {
    gap: var(--gap-base);
  }
  .frameParent {
    gap: var(--gap-mini);
  }
  .editprofile {
    gap: var(--gap-xl);
  }
}
