.facebookSetChild {
  height: 427px;
  flex: 1;
  position: relative;
  border-radius: var(--br-5xs);
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
}
.facebookIcon {
  height: 67.9px;
  width: 72px;
  position: absolute;
  margin: 0 !important;
  top: 11px;
  left: 307px;
  object-fit: contain;
  z-index: 1;
}
.facebookSet,
.facebookSetItem {
  position: relative;
  max-width: 100%;
}
.facebookSet {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-3xs);
}
.facebookSetItem {
  height: 427px;
  flex: 1;
  border-radius: var(--br-5xs);
  overflow: hidden;
  object-fit: cover;
}
.facebookIcon1 {
  height: 67.9px;
  width: 72px;
  position: absolute;
  margin: 0 !important;
  top: 11px;
  left: 311px;
  object-fit: contain;
  z-index: 1;
}
.facebookSet1 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  gap: var(--gap-3xs);
  max-width: 100%;
}
.facebookSetInner {
  height: 427px;
  flex: 1;
  position: relative;
  border-radius: var(--br-5xs);
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
}
.facebookIcon2 {
  height: 67.9px;
  width: 72px;
  position: absolute;
  margin: 0 !important;
  top: 11px;
  left: 308px;
  object-fit: contain;
  z-index: 1;
}
.facebookSet2 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  gap: var(--gap-3xs);
  max-width: 100%;
}
.facebookSetParent,
.frameWrapper {
  align-self: stretch;
  justify-content: flex-start;
  max-width: 100%;
}
.facebookSetParent {
  display: grid;
  flex-direction: row;
  align-items: flex-start;
  gap: var(--gap-47xl);
  grid-template-columns: repeat(3, minmax(291px, 1fr));
}
.frameWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.wibtaCommunity {
  display: inline-block;
  max-width: 100%;
}
.dateCommunity,
.wibtaCommunity {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 25px;
  font-weight: 700;
  font-family: inherit;
}
.dateCommunityWrapper {
  width: 352px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 var(--padding-12xs);
  box-sizing: border-box;
  max-width: 100%;
}
.ngabuburit {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 25px;
  font-weight: 700;
  font-family: inherit;
}
.wIBTA {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
  max-width: 100%;
  gap: var(--gap-xl);
}
.frameParent,
.wIBTAWrapper {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
  max-width: 100%;
}
.wIBTAWrapper {
  width: 1232px;
  flex-direction: row;
  padding: 0 var(--padding-14xl);
}
.frameParent {
  align-self: stretch;
  flex-direction: column;
  padding: 0 var(--padding-11xl) 0 0;
  gap: var(--gap-29xl);
  text-align: center;
  font-size: var(--font-size-13xl);
  color: var(--color-gold-100);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 1200px) {
  .facebookSetParent {
    justify-content: center;
    grid-template-columns: repeat(2, minmax(291px, 504px));
  }
  .wIBTA {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 825px) {
  .facebookSetParent {
    gap: var(--gap-14xl);
    grid-template-columns: minmax(291px, 1fr);
  }
  .frameWrapper {
    gap: var(--gap-5xl);
  }
  .dateCommunity,
  .ngabuburit,
  .wibtaCommunity {
    font-size: var(--font-size-7xl);
    line-height: 20px;
  }
  .frameParent {
    gap: var(--gap-5xl);
  }
}
@media screen and (max-width: 450px) {
  .facebookSetParent {
    gap: var(--gap-base);
  }
  .dateCommunity,
  .ngabuburit,
  .wibtaCommunity {
    font-size: var(--font-size-lgi);
    line-height: 15px;
  }
}
