.eventInfoChild {
  height: 109px;
  width: 5px;
  position: relative;
  border-right: 5px solid var(--color-white);
  box-sizing: border-box;
}
.frameChild {
  border: 0;
  outline: 0;
  background-color: var(--color-gray-500);
  width: 100%;
  height: 104px;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  left: 0;
}
.informasiSeputarEvent {
  width: 220px;
  height: 129px;
  position: relative;
  line-height: 25px;
  display: flex;
  align-items: center;
  flex-shrink: 0;
  z-index: 1;
}
.news {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 16px;
  font-weight: 400;
  font-family: inherit;
  display: inline-block;
  min-width: 89px;
  z-index: 1;
}
.infoTitle {
  flex-direction: row;
  padding: 0 var(--padding-11xs);
  margin-top: -122px;
  text-align: center;
  font-size: var(--font-size-13xl);
  color: var(--color-gold-100);
  font-family: var(--font-montserrat);
}
.eventInfo,
.infoTitle,
.rectangleParent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.rectangleParent {
  align-self: stretch;
  flex: 1;
  flex-direction: column;
  padding: var(--padding-2xs) var(--padding-mini) 0;
  position: relative;
  margin-left: -2px;
}
.eventInfo {
  width: 299px;
  height: 140px;
  flex-direction: row;
}
.vectorIcon,
.vectorIcon1 {
  align-self: stretch;
  width: 36px;
  position: relative;
  max-height: 100%;
  object-fit: contain;
  min-height: 61px;
}
.proPlayerInfo {
  width: 1382px;
  margin: 0 !important;
  position: absolute;
  right: -107px;
  bottom: 207px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  padding: 0 var(--padding-3xs) 0 0;
  box-sizing: border-box;
  gap: var(--gap-xl);
}
.frameInner,
.frameItem {
  border-radius: var(--br-xl);
}
.frameItem {
  height: 490px;
  width: 1172px;
  position: relative;
  object-fit: cover;
  display: none;
  max-width: 100%;
}
.frameInner {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--color-dimgray-300);
  mix-blend-mode: hard-light;
}
.proPlayerClub1 {
  margin-block-start: 0;
  margin-block-end: 10px;
}
.linkVyewnacapcut1as {
  margin: 0;
}
.proPlayerClubContainer {
  margin: 0;
  position: relative;
  font-size: inherit;
  font-weight: 700;
  font-family: inherit;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  z-index: 1;
}
.rectangleContainer,
.rectangleGroup {
  justify-content: flex-start;
  box-sizing: border-box;
  z-index: 1;
}
.rectangleContainer {
  width: 542px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: var(--padding-2xl) var(--padding-4xl);
  position: relative;
  max-width: 100%;
}
.rectangleGroup {
  flex: 1;
  border-radius: var(--br-xl);
  padding: var(--padding-5xl) var(--padding-9xl);
  background-image: url(/public/rectangle-10@2x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  min-height: 490px;
}
.proPlayerClub,
.proPlayerContent,
.rectangleGroup {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  max-width: 100%;
}
.proPlayerContent {
  width: 1172px;
  justify-content: flex-start;
  position: relative;
}
.proPlayerClub {
  align-self: stretch;
  justify-content: flex-end;
  font-size: var(--font-size-13xl);
  color: var(--color-gainsboro-100);
  font-family: var(--font-montserrat);
}
.eventsAndTournamentInfo,
.eventsAndTournamentInfoWrapper {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.eventsAndTournamentInfo {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-15xl);
}
.eventsAndTournamentInfoWrapper {
  width: 1379px;
  flex-direction: row;
  padding: 0 var(--padding-52xl) var(--padding-135xl);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--font-size-xl);
  color: var(--color-white);
  font-family: var(--font-roboto);
}
@media screen and (max-width: 1350px) {
  .eventsAndTournamentInfoWrapper {
    padding-left: var(--padding-16xl);
    padding-right: var(--padding-16xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1125px) {
  .eventsAndTournamentInfoWrapper {
    padding-bottom: var(--padding-81xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 800px) {
  .news {
    line-height: 13px;
  }
  .news,
  .proPlayerClubContainer {
    font-size: var(--font-size-7xl);
  }
  .eventsAndTournamentInfo {
    gap: var(--gap-mid);
  }
}
@media screen and (max-width: 450px) {
  .informasiSeputarEvent {
    font-size: var(--font-size-base);
    line-height: 20px;
  }
  .news {
    line-height: 10px;
  }
  .news,
  .proPlayerClubContainer {
    font-size: var(--font-size-lgi);
  }
  .eventsAndTournamentInfoWrapper {
    padding-bottom: var(--padding-46xl);
    box-sizing: border-box;
  }
}
