.postInfoChild {
  width: 1165px;
  height: 872px;
  position: relative;
  border-radius: var(--br-3xs);
  background-color: var(--color-darkslategray-400);
  display: none;
  max-width: 100%;
}
.slotMidBang {
  position: relative;
  line-height: 25px;
  z-index: 2;
}
.postContent,
.postInfo {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-15xl);
  box-sizing: border-box;
  max-width: 100%;
  font-size: var(--font-size-5xl);
}
.postInfo {
  align-self: stretch;
  border-radius: var(--br-3xs);
  background-color: var(--color-darkslategray-400);
  flex-direction: column;
  padding: var(--padding-xs) var(--padding-36xl) var(--padding-33xl-7);
  gap: var(--gap-9xl);
  z-index: 1;
  text-align: center;
  font-size: var(--font-size-13xl);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 1200px) {
  .postInfo {
    padding-left: var(--padding-8xl);
    padding-right: var(--padding-8xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1050px) {
  .postInfo {
    padding-top: var(--padding-xl);
    padding-bottom: var(--padding-15xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .slotMidBang {
    font-size: var(--font-size-lgi);
    line-height: 20px;
  }
  .postInfo {
    padding-bottom: var(--padding-3xl);
    box-sizing: border-box;
  }
}
