.gameInfo {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 25px;
  font-weight: 700;
  font-family: inherit;
  z-index: 1;
}
.infoHeader {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-54xl);
  box-sizing: border-box;
  max-width: 100%;
  font-size: var(--font-size-29xl);
}
.frameChild {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-5xs);
  background-color: var(--color-gold-100);
}
.maps {
  position: relative;
  line-height: 25px;
  font-weight: 500;
  display: inline-block;
  min-width: 66px;
  z-index: 1;
}
.rectangleParent {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-base) var(--padding-36xl);
  position: relative;
}
.frameItem {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-5xs);
  background-color: var(--color-white);
}
.weapon {
  position: relative;
  line-height: 25px;
  font-weight: 500;
  display: inline-block;
  min-width: 103px;
  z-index: 1;
}
.rectangleGroup {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-base) var(--padding-18xl);
  position: relative;
  cursor: pointer;
}
.frameInner {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-5xs);
  background-color: var(--color-white);
}
.vehicles {
  position: relative;
  line-height: 25px;
  font-weight: 500;
  display: inline-block;
  min-width: 101px;
  z-index: 1;
}
.rectangleContainer {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-base) var(--padding-19xl);
  position: relative;
}
.rectangleDiv {
  height: 100%;
  width: 177px;
  position: absolute;
  margin: 0 !important;
  top: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-5xs);
  background-color: var(--color-white);
}
.arcade {
  position: relative;
  line-height: 25px;
  font-weight: 500;
  display: inline-block;
  min-width: 87px;
  z-index: 1;
}
.frameChild1 {
  height: 100%;
  width: 177px;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  border-radius: var(--br-5xs);
  background-color: var(--color-white);
}
.event {
  position: relative;
  line-height: 25px;
  font-weight: 500;
  display: inline-block;
  min-width: 70px;
  z-index: 1;
}
.button,
.buttonWrapper,
.groupDiv {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  max-width: 100%;
}
.groupDiv {
  flex: 1;
  justify-content: space-between;
  padding: var(--padding-base) var(--padding-35xl) var(--padding-base)
    var(--padding-26xl);
  box-sizing: border-box;
  position: relative;
  min-width: 157px;
  gap: var(--gap-xl);
}
.button,
.buttonWrapper {
  justify-content: center;
}
.button {
  width: 1011px;
  flex-wrap: wrap;
  gap: var(--gap-11xl);
  z-index: 1;
}
.buttonWrapper {
  width: 1197px;
  padding: 0 var(--padding-xl);
  box-sizing: border-box;
  color: var(--color-black);
}
.mapImageContainer {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--color-lightgray-100);
  mix-blend-mode: hard-light;
  z-index: 1;
}
.image2Icon,
.mapImageBorder {
  position: absolute;
  object-fit: cover;
}
.image2Icon {
  top: 107px;
  left: -904px;
  width: 2318px;
  height: 1304px;
  mix-blend-mode: luminosity;
}
.mapImageBorder {
  top: 0;
  left: 32px;
  border-radius: var(--br-5xs);
  width: 287px;
  height: 314px;
  z-index: 2;
}
.mapImageWrapper {
  height: 1411px;
  width: 1414px;
  position: absolute;
  margin: 0 !important;
  top: -624px;
  left: 0;
}
.descriptionContainerIcon {
  align-self: stretch;
  height: 291px;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
  cursor: pointer;
  z-index: 2;
}
.sanhok,
.sanhokPetaKecil {
  position: relative;
  line-height: 25px;
  z-index: 2;
}
.sanhokPetaKecil {
  align-self: stretch;
  height: 268px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}
.sanhok {
  margin: 0;
  font-size: inherit;
  font-weight: 800;
  font-family: inherit;
}
.sanhokTitleContainer {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-3xl) 0 var(--padding-xl);
  margin-top: -235px;
  font-size: var(--font-size-17xl);
  color: var(--color-olive);
}
.sanhokPetaKecilPertempuraParent {
  flex: 1;
  flex-direction: column;
  min-height: 268px;
}
.descriptionWrapper,
.mapDescription,
.sanhokPetaKecilPertempuraParent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.descriptionWrapper {
  align-self: stretch;
  flex-direction: row;
  padding: 0 0 0 var(--padding-11xs);
  box-sizing: border-box;
  margin-top: -13px;
}
.mapDescription {
  flex-direction: column;
}
.detailsDividerIcon {
  height: 74.7px;
  width: 43px;
  position: absolute;
  margin: 0 !important;
  top: 251.3px;
  right: 59px;
  object-fit: contain;
  z-index: 2;
}
.infoHeaderParent,
.mapDetails {
  align-self: stretch;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
  max-width: 100%;
}
.mapDetails {
  display: grid;
  flex-direction: row;
  padding: var(--padding-16xl) var(--padding-71xl);
  position: relative;
  gap: var(--gap-73xl);
  grid-template-columns: repeat(3, minmax(250px, 1fr));
  color: var(--color-darkslateblue);
}
.infoHeaderParent {
  display: flex;
  flex-direction: column;
  padding: 0 0 var(--padding-130xl);
  gap: var(--gap-37xl-5);
  flex-shrink: 0;
  text-align: center;
  font-size: var(--font-size-5xl);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 1350px) {
  .mapDetails {
    padding-left: var(--padding-26xl);
    padding-right: var(--padding-26xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1125px) {
  .mapDetails {
    justify-content: center;
    grid-template-columns: repeat(2, minmax(250px, 434px));
  }
  .infoHeaderParent {
    padding-bottom: var(--padding-78xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 800px) {
  .gameInfo,
  .sanhok {
    font-size: var(--font-size-19xl);
    line-height: 20px;
  }
  .sanhok {
    font-size: var(--font-size-10xl);
  }
  .mapDetails {
    gap: var(--gap-27xl);
    padding: var(--padding-4xl) var(--padding-3xl);
    box-sizing: border-box;
    grid-template-columns: minmax(250px, 1fr);
  }
  .infoHeaderParent {
    gap: var(--gap-9xl);
    padding-bottom: var(--padding-44xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .gameInfo {
    font-size: var(--font-size-10xl);
    line-height: 15px;
  }
  .infoHeader {
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
  .arcade,
  .event,
  .maps,
  .vehicles,
  .weapon {
    font-size: var(--font-size-lgi);
    line-height: 20px;
  }
  .groupDiv {
    flex-wrap: wrap;
    justify-content: center;
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
  .sanhokPetaKecil {
    font-size: var(--font-size-lgi);
    line-height: 20px;
  }
  .sanhok {
    font-size: var(--font-size-3xl);
    line-height: 15px;
  }
  .mapDetails {
    gap: var(--gap-4xl);
  }
}
