.skinUpdateContentChild {
  align-self: stretch;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
  z-index: 1;
}
.newSkin {
  width: 127px;
  position: relative;
  line-height: 25px;
  font-weight: 800;
  display: inline-block;
  min-width: 127px;
  z-index: 1;
}
.newSkinWrapper,
.skinUpdateContent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.newSkinWrapper {
  flex-direction: row;
  padding: 0 var(--padding-11xs);
}
.skinUpdateContent {
  height: 336px;
  width: 329px;
  flex-shrink: 0;
  flex-direction: column;
  gap: var(--gap-xl);
  max-width: 100%;
  text-align: center;
  font-size: var(--font-size-5xl);
  color: var(--color-white);
  font-family: var(--font-roboto);
}
@media screen and (max-width: 450px) {
  .newSkin {
    font-size: var(--font-size-lgi);
    line-height: 20px;
  }
}
